.dx-switch {
  width: 100%;
  height: 100%;
}

.dx-switch-container {
  height: 100% !important;
  border-radius: 4px;
  padding: 0px 5px;
}

.dx-switch-handle {
  height: auto;
  padding: 3px 0px;
}

.dx-switch-on,
.dx-switch-off {
  font: inherit !important;
  padding: 3px 15px 4px 4px;
  align-self: center;
}

.dx-form .dx-switch-on,
.dx-form .dx-switch-off {
  padding: 7px 20px 8px 8px;
}

.dx-data-row .dx-switch .dx-switch-container{
  background-color: white !important;
}

.no-disabled {
  & .dx-switch-off{
  color:#5e5e5e;
  }

  & .dx-switch-handle:before{
    background-color: #edb637;
  }
}

.dxSwitch_toolbar{
  .dx-switch-on,
  .dx-switch-off {
    padding: 7px 20px 8px 8px;
  }
} 

