#SolicitudesAlta-Grid {
  .icon-anulado:before {
    border: 2px solid #d92550;
    border-radius: 50px;
    padding: 3px;
  }

  .validar-button {
    border: 1px solid var(--success);

    padding: 0;
    width: fit-content;
    display: flex;
    justify-content: center;
    align-items: stretch;

    overflow: hidden;

    &:hover {
      background-color: color-mix(in srgb, var(--success) 10%, transparent);
      cursor: pointer;
    }

    &:active {
      background-color: color-mix(in srgb, var(--success) 20%, transparent);
    }

    :first-child {
      min-height: 100%;
      padding: 0 5px;
      color: var(--success) !important;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
