.CustomCookieBanner {
    position: fixed;
    bottom: 0;
    display: flex;
    width: 100%;
    z-index: 2000;
    background-color: whitesmoke;
    border-top: 2px solid #0e4174;
    padding: 15px;

    & .title {
        font-size: 24px;
        color: rgb(65, 65, 65);
        padding-bottom: 10px;
    }

    & .message {
        font-size: 12px;
    }

    & .closeButton {
        position: absolute;
        top: 0px;
        right: 0px;
        border: none;
        font-size: 35px;
        width: 50px;
        height: 50px;
    }

    & .acceptButton {
        float: right;
        font-size: 14px;
        :before {
            font-size: 22px;
        }
    }
}
