.dx-form .dx-field-item-label-text{
    font-weight: 700;
    color: var(--secondary)
}

.pb-0 .dx-form-group-content{
    padding-bottom:0px;
}


.dxForm_group{
    margin-top: 6px;
    margin-left: 6px;
    margin-right: 6px;
    padding: 10px 20px;
    border-radius: 15px;
    box-shadow: 0px 1px 4px 0px #ccc;
    margin-bottom: 10px;
    height: 100%;

    .countryContainer
    {
        width: calc(100% - 35px) !important;
        .countryText{
            display: flex;
            justify-content: flex-end;
        }

        .countryFlag{
            background-position: right !important;
            width: 100% !important;
            margin-top: 2px;
        }
    }

    .dx-lookup{
        border: none;
        border-bottom: 1px solid #ddd;
        width: 100% !important;

        .dx-lookup-field{
            padding-right: 0px;
        }
    }

    .dx-selectbox input, .dx-selectbox .dx-placeholder {  
        text-align: end !important; 
        width: 100%; 
    }  

    .inputColor_blue {
        .dx-tile{
            border-color:var(--blue) !important;
        }
    }

    .dx-tileview{
        height: 120px !important;
        .dx-tile{
            top:10px !important;
            float:right;
            position:relative;
        }
    }

    .dx-first-row.dx-first-col.dx-last-col.dx-field-item{
       padding-top: 10px;
    }

    img{
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
    }

    /*TOOLBAR ELIMINAR IMAGENES*/

    & .iconos_svg
    {
        height:25px;
        width:25px;
        cursor:pointer;
        font-size:25px;
    }

    & #contenedorImg {
        height: 100%;
        & > .image {
        height: 100%;
        width: 100%;
        background-position: center;
        background-size: cover;
        }
    }


    & .dx-item.dx-state-hover,
    & .dx-item.dx-state-active {
        & .dxToolbar_opciones {
        display: block;
        width: 36px !important;
        height: 45px;
        padding:5px;
        padding-top:4px;
        }
    }

    .dxToolbar_position
    {
        position:absolute;
        bottom:-5px;
        right:0px;
    }
}