.img-preview-doc {
    border: 1px solid #e0e0e0;
    border-radius: 10px;

    img {
        object-fit: contain;
    }

    &:hover {
        cursor: pointer;
        border-color: var(--primary);
    }
}

.visualizadorFotos-popup-container {
    img {
        border-radius: 10px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);

        object-fit: contain;

        max-height: fit-content;
        height: 100%;
        max-width: fit-content;
        width: 100%;
    }
}
