#CalendarioLaboral_RRHH{
    .gridContainer{
        display: grid;
       grid-template-columns: repeat(5,calc(20% - 8px));
        border-radius: 12px;
        align-content: flex-start;
        cursor: default;
        gap: 0px 10px;
    }

    .actionToastIcon{
        pointer-events: none;    
        justify-content: center;
        line-height: initial;
        height: 100%;
    }    

    .toastIconContainer{
        background-color: var(--notificacionColor);
        padding: 2px 10px;
        width: 45%;
        border-radius:5px 0px 0px 5px;
    }

    .actionToastDescripcion{
        padding:0 10px;
    }

    .actionToast-container
    {
        border: 1px solid var(--notificacionColor);
        border-left: 1px solid var(--notificacionColor);
        border-radius:8px;
        box-shadow:0px 0px 5px 1px #ccc;
        transition: transform .2s;
    }
}
