#Notificaciones {
  @for $porc from 0 through 100 {
    .he-#{$porc} {
      height: percentage($porc / 100);
    }
  }

  .iconHeader_dxButton_comunicado .dx-icon.dx-icon-plus {
    font-size: x-large;
  }
  .he-card {
    height: 320px;
  }

  #dxScrollView_principal {
    .dx-scrollable-content:first-child {
      padding: 0px;
    }

    .dx-scrollview-content:first-child {
      padding: 0px;
    }
  }

  .dx-scrollview {
    .dx-scrollable-content:first-child {
      height: 100%;
    }
    .dx-scrollview-content {
      height: 100%;
      padding: 0px;
      padding-top: 5px;
      padding-bottom: 5px;
    }

    .dx-scrollable-container {
      padding: 0px;
      padding-top: 0px;
      padding-bottom: 0px;
    }
  }

  .cursor-default .dx-item.dx-list-item {
    cursor: default;
  }

  .miniCalendar_container {
    width: 60px;
    font-weight: 600;
    padding-bottom: 3px;
    font-size: 25px;

    flex: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(245, 245, 245);
    overflow: hidden;
    border-radius: 15px;
    text-align: center;
  }
  .miniCalendar_header {
    color: white;
    font-weight: 600;
    font-size: 12px;
    text-transform: uppercase;

    text-align: center;
    background-color: #edb637;
    width: "100%";
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .dx-calendar-cell {
    cursor: default;

    .cellContainer {
      pointer-events: none;
    }

    .cellContainer > div:first-child {
      font-size: 14px;
      line-height: 100%;
    }
    .cellContainer > div:nth-child(2n) {
      height: 5px;
      margin-top: 4px;
    }
  }

  .card-body {
    height: calc(100% - 3.5rem);
  }

  .dx-scrollbar-vertical {
    right: -10px;
  }

  .body-actionToast {
    padding: 17.5px 5px;

    .dx-tabs {
      .dx-tab-content {
        width: 100%;
      }

      .dx-tab-text {
        align-items: flex-end;
        padding-bottom: 8px;
      }

      .dx-tab.dx-badge {
        right: 5px;
      }

      i {
        font-size: xx-large;
        line-height: 35px;
      }
    }
  }
}

.dxcalendar-date-circle {
  background-color: var(--circleColor);
  border: 1px solid var(--circleColor);
  height: var(--circleSize);
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  width: var(--circleSize);
}

.dxLookupPopup_filtrarNotificaciones {
  background: #fff;
  width: 100%;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.18);
  .dx-list-item-icon-container {
    display: none;
  }
}

.containerFiltroNotificaciones {
  .dx-lookup-field {
    text-transform: capitalize !important;
  }
  width: calc(100% - 135px) !important;
}

.personasIncluidas_container {
  overflow: hidden;
  border-radius: 8px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.personasIncluidas_header {
  padding: 10px;
  background-color: #f7f5f7;
  color: white;
  border: none;
  background-color: var(--success);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
