#Login,
#ForgotPassword,
#RecoveryPassword {
    line-height: 35px;
    position: relative;
    opacity: 0.99;
    font-size: 14px;
    border-radius: 0px !important;

    .welcomeContainer {
        .welcomeTitle {
            font-size: 22px !important;
            font-weight: bold;
        }
    }

    // Pseudo-elemento para las imgenes de fondo
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0;
        transition: opacity 1s ease-in-out; // Transicin para el fade-in
        z-index: -1; // Se queda detrs del contenido
    }

    @media (min-width: 0) {
        background-color: #f1be48;
    }
    // Imagen para dispositivos mviles por defecto (768px)
    @media (min-width: 768px) {
        &::before {
            background-image: url("../../../../assets/utils/images/login/IMG_960px.jpg");
            opacity: 1; // Aparece suavemente
        }
    }

    // Imagen para tabletas (1280px)
    @media (min-width: 1280px) {
        &::before {
            background-image: url("../../../../assets/utils/images/login/IMG_2048px.jpg");
            opacity: 1; // Aplicamos el efecto de fade-in
        }
    }

    // Imagen para pantallas grandes (1440px)
    @media (min-width: 1440px) {
        &::before {
            background-image: url("../../../../assets/utils/images/login/IMG_2880px.jpg");
            opacity: 1;
        }
    }

    // Imagen para pantallas de 1920px
    @media (min-width: 1920px) {
        &::before {
            background-image: url("../../../../assets/utils/images/login/IMG_3840px.jpg");
            opacity: 1;
        }
    }

    // Imagen para pantallas 4K (3840px)
    @media (min-width: 3840px) {
        &::before {
            background-image: url("../../../../assets/utils/images/login/IMG_7680px.jpg");
            opacity: 1;
        }
    }

    // background-color: #f1be48;
    // clip-path: polygon(100% 0%, 50% 50%, 100% 100%);
    // opacity: 0.2;
    color: #0e4174;
    a {
        color: #0e4174;
    }

    @media (min-width: 768px) {
        #shapesContainer {
            // filter: drop-shadow(1px 2px 3px rgba(50, 50, 0, 0.5));
        }
        #bgShape1 {
            background-color: #f1be48;
            clip-path: polygon(0% 0%, 30% 0%, 70% 100%, 0% 100%);
        }

        #bgShape2 {
            background-color: white;
            opacity: 0.5;
            clip-path: polygon(50% 50%, 60% 50%, 80% 100%, 70% 100%);
        }
    }

    #submitBtn {
        background-color: #ff7300;
        font-size: 16px;
        border-radius: 0px;
    }

    .dx-texteditor {
        &,
        .dx-texteditor-input {
            color: #0e4174;
            font-size: 14px;
        }
        &.dx-editor-outlined {
            border-radius: 0px;
            border: none;

            .dx-placeholder {
                font-style: italic;
                color: var(--secondary);
            }
        }
    }

    .loader-container {
        height: 50px;
        .loader {
            .line-scale > div {
                background-color: white;
            }
        }
    }
}