.dx-item.dx-buttongroup-item {
    width: 100%;
    border:none;
    background-color: transparent !important;
    .dx-button-content{
      padding:6px;
    }
    .dx-button-content > div{
      border-radius:5px;
      padding:7px 12px;
      box-shadow: 0px 0px 5px 1px #ccc;
    }
}

.dx-buttongroup-item:not(.dx-state-hover, .dx-item-selected){
    i{
        color:var(--buttonGroup_color) !important; 
    }
    span{
        font-weight: 500;
    }
}

.dx-buttongroup-item.dx-state-hover:not(.dx-item-selected){
    .dx-button-content > div{
        background-color:var(--buttonGroup_color) !important; 
    }

    i,span{
        color:white !important;
    }
  
    span{
        font-weight: 500;
    }
}
.dx-buttongroup-item.dx-item-selected, .dx-buttongroup-item.dx-state-hover{  
    .dx-button-content > div{
        background-color:var(--buttonGroup_color) !important;
    }

    i,span{
      color:white !important;
    }

    span{
        font-weight: 500;
    }
}

.dx-buttongroup-item-content:not(.dx-item-selected){    
    i,span{
      color:var(--buttonGroup_color);
    }
    span{
        font-weight: 500;
    }
    .dx-checkbox-icon:before{
        color:#edb637;
    }
}