.CuadranteCalendario {
  background-color: transparent !important;
}

.contenedorCentrado {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.tooltipEvento {
  display: flex;
  flex-direction: column;
  align-items: start;
  border-radius: 15px;

  font-size: 0.95rem;
  font-weight: 700;

  box-shadow: 0 2px 4px rgb(0, 0, 0, 0.1);

  & span:first-child {
    width: 100%;
    text-align: center;
    font-size: 1rem;
    border-color: inherit;
  }
}

.tooltipEvento:not(:last-child) {
  margin-bottom: 0.5rem;
}
