#ActionToast{
    @for $porc from 0 through 100 {
        .w-#{$porc} {
            width: percentage($porc /100);
        }
    }

    .textOverflowEllipsis{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 95%;
    }


    .actionToastTitle{
        color:black;
        font-weight: 500;
    }
    
    .actionToast-container
    {
        border: 1px solid var(--notificacionColor);
        border-left: 5px solid var(--notificacionColor);
        border-radius:8px;
        padding:15px 16px 17px 16px; 
        box-shadow:0px 0px 5px 1px #ccc;
        transition: transform .2s;
        cursor: pointer;
    }
    

    .comunicado_editable{
        align-items: center;
        height: auto;
        // padding-bottom: 20px;
        i{
            color: gray;
        }
        i:hover{
            color:var(--primary);
        }
    }
    
    .actionToast-container:hover{
        transform: scale(1.03);
    }
    
    .actionToastIcon{
        color: var(--notificacionColor) !important;
        pointer-events: none;
        line-height: initial;
    }    
    
    .center-text-comunicado{
        width:calc(100% - 60px);
        text-align: center;
    }
}
.ActionToast_dxForm_group{
    margin-top: 6px;
    margin-left: 6px;
    margin-right: 6px;
    padding: 10px 20px;
    border-radius: 15px;
    box-shadow: 0px 1px 4px 0px #ccc;
    margin-bottom: 10px;

    .dx-selectbox input, .dx-selectbox .dx-placeholder {  
        text-align: end !important; 
        width: 100%; 
    }  

    .inputColor_blue {
        .dx-tile{
            border-color:var(--blue) !important;
        }
    }

    .dx-tileview{
        height: 120px !important;
        .dx-tile{
            top:10px !important;
            float:right;
            position:relative;
        }
    }

    .dx-first-row.dx-first-col.dx-last-col.dx-field-item{
        padding-top: 10px;
    }

    img{
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
    }

    /*TOOLBAR ELIMINAR IMAGENES*/

    & .iconos_svg
    {
        height:25px;
        width:25px;
        cursor:pointer;
        font-size:25px;
    }

    & #contenedorImg {
        height: 100%;
        & > .image {
        height: 100%;
        width: 100%;
        background-position: center;
        background-size: cover;
        }
    }


    & .dx-item.dx-state-hover,
    & .dx-item.dx-state-active {
        & .dxToolbar_opciones {
        display: block;
        width: 36px !important;
        height: 45px;
        padding:5px;
        padding-top:4px;
        }
    }

    .dxToolbar_position
    {
        position:absolute;
        bottom:-5px;
        right:0px;
    }
}

.inputColor_blue input, .inputColor_blue .countryText{
    color:var(--blue);
}

#dxPopup_galeriaArchivos{
    .dx-first-col.dx-last-col.dx-field-item.dx-col-0.dx-field-item-optional {
        padding-top: 6px !important;
    }
    .revertirBtn{
        border:none;
    }
}

#dxPopup_editActionToast{
    padding-right:0px;

    .errorValidacion_img .dx-item.dx-tile{
        border-color: #d9534f !important;
    }

    .dx-lookup{
        border: none;
        border-bottom: 1px solid #ddd;
        width: 100% !important;

        .dx-lookup-field{
            padding-right: 0px;
        }
    }

    .countryContainer{
        width: calc(100% - 35px) !important;
    }

    .countryText{
        display: flex;
        justify-content: flex-end;
        font-weight: 500;
        width: fit-content !important;
        padding-left: 10px;
    }

    .countryFlag{
        background-position: right !important;
        margin-top: 2px;
    }

    .pe-none {
        .countryContainer{
            width: 100% !important;
        }
        .dx-lookup{
            border: none;
        }
        .dx-lookup-arrow{
            display:none;
        }
    }

    #dxScrollView_noficacionPersona > div > div > .dx-scrollable-content{
        padding-right:20px;
    }

    #dxScrollView_noficacionPersona {
        .dx-first-col.dx-last-col.dx-field-item.dx-col-0.dx-field-item-optional {
            padding-top: 0px !important;
        }

        .ActionToast_dxForm_group .dx-responsivebox > div > .dx-item.dx-box-item{
            padding-top: 15px;
        }

        .readOnlyContainer{
            height: fit-content;
            font-size: 14px;
            line-height: 19px;
        }

        .readOnlyContainer input, .readOnlyContainer .dx-texteditor-input{
            border:none;
            text-align: end;
            font-weight: 500;
        }
    }

    .dx-scrollable-scrollbar{
        margin-right:9px;
    }
}

.circular_tileView .dx-item.dx-tile{
    border-radius:50% !important;
    width: 100% !important;

    .dx-toolbar.dx-widget{
        padding-right: 18%; //35%; solo para 1 item
    }

    .dx-item-content.dx-tile-content
    {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        overflow: hidden;
        position: relative;
    }

    img{
        height: 100%;
        width: 100%;
        background-position: center;
        background-size: cover;
       transform: scale(1.02) !important;
    }
}

#dxPopup_editActionToast{
    .revertirBtn{
        border:none;
    }

     
    .revertirBtn + .react-tel-input {
        border-radius: 0px !important;
        border-bottom: 1px solid #CACACA !important;
    }
    
    .readOnlyContainer .react-tel-input .flag .arrow {
        display: none;
    }

    .readOnlyContainer .react-tel-input input{
        border-bottom: none !important;
    }
    .react-tel-input .selected-flag:hover, .react-tel-input .selected-flag:focus{
        background-color: transparent;
    }
}