.contenedor-observaciones {
    width: 100%;
    overflow: hidden;
}

.span-observaciones {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.span-observaciones-op {
    opacity: 0;
    position: absolute;
    z-index: -1;
}

.tooltip-observaciones {
    max-width: 300px;
    white-space: normal;
    display: flex;
    flex-direction: column;
    text-align: left;
}