.dx-radio-value-container {
  display: none;
}

.dx-radiogroup {
  &.dx-radiogroup-vertical > .dx-collection {
    flex-direction: column;
    & .dx-radiobutton {
      width: 100%; //Firefox
    }
  }
  & > .dx-collection {
    display: flex;

    & .dx-radiobutton {
      flex: 1;
      padding: 10px 30px;
      border-radius: 3px;
      font-size: 1rem;
      min-width: 150px; //Firefox
      max-width: 400px;

      &.dx-item-selected {
        background-color: var(--primary);
        border: 1px solid var(--primary);
        color: white;
      }

      &:not(.dx-item-selected) {
        border: 1px dotted gainsboro;
      }
    }
  }
}
