.dx-calendar-border{
    border-radius: 4px;
    &:not(.dx-invalid) {
        border: 1px solid #ddd;
    }

    .dx-calendar-navigator-next-view.dx-button, .dx-calendar-navigator-previous-month.dx-button {
        margin: 0;
        border-left: none;
        border-right: none;
        border-top: none;
        border-radius: 0;
    }

    .dx-calendar-caption-button {
        border-top: none;
    }
}

.personasIncluidas-invalid {
    outline: 1px solid rgba(217, 83, 79, 0.4);
    .dx-datagrid-nodata{
        color: #d9534f;
        &::after{
            pointer-events: none;
            font-weight: 700;
            position: absolute;
            top: 50%;
            margin-left: 10px;
            margin-top: -9px;
            width: 18px;
            height: 18px;
            text-align: center;
            line-height: 18px;
            font-size: 13px;
            background-color: #d9534f;
            color: #fff;
            content: '!';
            border-radius: 50%;
        }
    }
}