.chip {
    display: inline-block;
    padding: 1px 10px;
    border-radius: 25px;

    &.chip-hover{
        &:hover{
            filter: brightness(0.9);
        }
    }

    &.chip-active{
        &:active{
            filter: brightness(0.8);
        }
    }

    &.danger {
        color: white;
        background-color: var(--red);
    }
    &.success {
        color: white;
        background-color: var(--teal);
    }
    &.warning {
        color: white;
        background-color: var(--warning);
    }
    &.orange {
        color: white;
        background-color: var(--orange);
    }
    &.primary {
        color: white;
        background-color: var(--primary);
    }
    &.default {
        color: white;
        background-color: var(--gray);
    }
    &.alternate {
        color: white;
        background-color: var(--purple);
    }
    &.large {
        font-size: large;
        padding: 2px 14px;
    }
    &.larger {
        font-size: larger;
        padding: 2px 14px;
    }
    &.x-large {
        font-size: x-large;
        padding: 2px 14px;
    }
    &.xx-large {
        font-size: xx-large;
        padding: 3px 18px;
    }

    &.font-size-xxxxs {
        font-size: 0.8rem;
        padding: 2px 10px;
    }

    &.font-size-xxxs {
        font-size: 0.9rem;
        padding: 2px 12px;
    }

    &.font-size-xxs {
        font-size: 1rem;
        padding: 2px 14px;
    }

    &.font-size-xs {
        font-size: 1.2rem;
        padding: 3px 14px;
    }

    &.font-size-sm {
        font-size: 1.25rem;
        padding: 3px 14px;
    }

    &.font-size-md {
        font-size: 1.1rem;
        padding: 1.5px 13px;
    }

    &.font-size-lg {
        font-size: 1.4rem;
        padding: 4px 16px;
    }

    &.font-size-xl {
        font-size: 1.7rem;
        padding: 4px 18px;
    }

    &.font-size-xxl {
        font-size: 2.3rem;
        padding: 5px 18px;
    }

    &.font-size-xxll {
        font-size: 2rem;
        padding: 4.5px 18px;
    }

    &.font-size-xxxl {
        font-size: 2.6rem;
        padding: 4.5px 20px;
    }

    &.font-size-xxxxl {
        font-size: 2.9rem;
        padding: 5px 20px;
    }

    &.font-size-max {
        font-size: 3.1rem;
        padding: 6px 22px;
    }
}
