#dxTileView_documentos {
  /*Mostrar imagenes en el centro*/
  & #contenedorImg {
    height: 100%;
    & > .image {
      height: 100%;
      width: 100%;
      background-position: center;
      background-size: cover;
    }
  }

  /*Mostrar las imagenes en la galera*/
  & .image,
  .image {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  & .dx-item.dx-state-hover,
  & .dx-item.dx-state-active {
    & .dxToolbar_opciones {
      display: block;
      width: 70px !important;
    }
  }
}
