.posicion {

    height: 4.5rem;
    width: 4.5rem;
    overflow: hidden;
    border-radius: 50px;

    & div,img {
        outline: 3px solid white;
        outline-offset: -1px;
        width: 100%;
        height: 100%;
        border-radius: 50px;
        background-position: center !important;
        background-size: cover !important;
    }

}

.sin-foto {
    background: var(--primary);
    color: white;

    & span {
        display: block;
        width: 100%;
        text-align: center;
        font-size: 1.25rem;
        font-weight: 700;
        white-space: pre;
    }
}

.MaquinaBox {
    
    .dx-box-item {
        max-height: 20rem;
        max-width: 100%;
        min-width: 100%;
    }
}

.tooltipPersona {
    height: 15.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;

    & .foto {
        background-color: var(--primary);
        margin: 10px;
        height: 100%;
        aspect-ratio: 1/1;
        border-radius: 25px;
        background-position: center !important;
        background-size: cover !important;
        overflow: hidden;
    }

    & .nombrePersona {
        width: 100%;
        text-align: center;
    }
}

.tooltipMaquina {
    width: 30rem;
    background: #f5f5f5;
    padding: 0.625rem;

    .tooltipMaquinaCard {
        border-radius: 0.5rem;
    
        .subCointainer div {
            border-radius: 0.25rem;
        }

        .nombreMaquina {
            font-weight: bold;
            font-size: 1rem;
            text-align: start;
            overflow: hidden;
            text-overflow: ellipsis
        }
    
        .clienteDenominacion {
            width: 100%;
            font-weight: 600;
            font-size: 1.2rem;
            display: block;
            text-align: start;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    
        .programaDenominacion {
            width: 100%;
            font-weight: 500;
            font-size: 1.05rem;
            display: block;
            text-align: start;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    
        .cardUnidades {
    
            & :first-child {
                font-weight: bold;
                font-size: 3rem;
            }
            & :last-child {
                font-size: 1.25rem;
                color: var(--secondary);
            }
        }
    
        .cardResumen {
            & :first-child {
                font-weight: bold;
                font-size: 2.4rem;
                margin-right: 5px;
            }
            & :last-child {
                font-size: 1.2rem;
            }
        }
    }
}

.timer {
    display: flex;
    flex-direction: row;
    align-items: center;
    // flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    font-weight: 700;

    border-radius: 50px;
    width: fit-content;
    margin: 0 auto;

    color: white;

    i:before {
        font-weight: 600;
        color: white;
    }
}

.stack-item {
    min-width: 0;
    max-width: 50%;
    & .foto, .sin-foto {
        border: 0.25rem solid white;
    }
    & .sin-foto:not(.foto) {
        text-decoration: underline;

        & :hover {
            color: var(--primary);
            text-decoration-color: var(--primary);
            cursor: pointer;
        }
    }
    & :hover {
        position: relative;
        z-index: 10;
    }
}