#CalendarioPersonal_InformeEventosPersona {
  & .month-container {
    width: 20% !important;
  }
}

.containerListaPersonas {
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  background-color: white;
  overflow: hidden;
}

.horas-mark {
  width: 15px;
  outline: 2px solid var(--danger);
  outline-offset: -1px;
  // border-radius: 10px;
}

.dx-switch-header-active {
  .dx-switch-container {

    .dx-switch-on,
    .dx-switch-off {
      color: inherit;
    }

    .dx-switch-handle:before {
      background-color: #edb637;
    }
  }
}