.jornadas {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 1rem 0;
  min-height: 30rem;
  align-items: center;
  justify-content: center;
}

.jornadaContainer {
  border-radius: 1.25rem;
  width: fit-content;

  // outline: 2px solid var(--smokedWhite);
  background-color: var(--smokedWhite);
  padding: 0.15rem;

  & > div {
    gap: 0.2rem;
  }

  & .jornadaCard {
    padding: 0.5rem;
    border-radius: 1rem;

    & .cuadranteBody {
      background: white;
      border-radius: 0.5rem;
    }
  }

  & .jornadaTable {
    th {
      border-bottom: 1px solid var(--lightGray);
      width: 100%;

      .editarCuadrante:hover {
        cursor: pointer;
        filter: brightness(90%);
        -webkit-filter: brightness(90%);
      }
    }

    .title {
      text-align: center;
      padding: 0.25rem;
      font-weight: 700;
      font-size: 1.1rem;
    }

    .editarCuadrante {
      display: flex;
      align-items: center;
      gap: 5px;
      padding: 2.5px;
      color: var(--primary);
    }

    td {
      padding: 0.25rem 0.5rem;
      position: relative;
      height: 50px;
      text-align: center;
      width: 11rem;
      overflow: hidden;

      & .LottieIconWarning {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0.5rem;

        display: flex;
        flex-direction: column;
        justify-content: center;

        pointer-events: none;

        & ::before {
          font-size: 1rem;
        }
      }

      & .input-label {
        background-color: white;
        color: var(--secondary);
        margin-top: -1.5px;
        margin-left: 20px;
        pointer-events: none;
        font-size: 0.9rem;
        border-radius: 7.5px;
      }
    }
  }

  & .controlJornadaCard {
    padding: 0.5rem;
    border-radius: 1rem;
    background-color: white;

    & .jornadaTable {
      width: 16rem;
    }

    & .controlJornadaTitle {
      width: 100%;
      padding: 0.25rem 0;
      padding-top: 12.5px;
      color: var(--red);
      font-weight: 700;
      text-align: center;
      display: block;
    }
  }

  & .jornadaDateLabel {
    display: block;
    width: 100%;
    font-weight: 600;
    font-size: 1rem;
    text-align: left;
  }

  & .dx-datebox-time {
    & .dx-texteditor-input {
      text-align: center;
      padding: 0.2rem !important;
    }

    & .dx-placeholder {
      color: var(--secondary);
      width: 100%;
      text-align: center;
    }
  }

  & .jornadaEditor {
    width: 100%;
    min-width: 11rem;
    border-radius: 0.75rem;

    & .dx-button {
      margin: 0;
      height: 100%;
      border-radius: 0;
    }

    & .dx-texteditor-buttons-container:last-child .dx-button:last-child,
    .solicitudExtras .dx-button:last-child {
      border-start-end-radius: 0.7rem;
      border-end-end-radius: 0.7rem;
    }

    & .dx-texteditor-buttons-container:first-child .dx-button:first-child,
    .solicitudExtras .dx-button:first-child {
      border-radius: 0;
      border-start-start-radius: 0.7rem;
      border-end-start-radius: 0.7rem;
    }

    & > span {
      height: 20px;
      display: flex;
      align-items: center;
      padding-inline: 5px;
    }
  }

  & .editorButtonsAbsolute {
    & .dx-texteditor-buttons-container {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
    }
  }
}

.jornadaFotoPersona {
  width: 100%;
  height: 100%;

  aspect-ratio: 1 / 1;
  border-radius: 5rem;

  overflow: hidden;
  position: relative;

  & img {
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;

    top: 50%;
    transform: translateY(-50%);
  }
}

.errorEstado {
  gap: 0.5rem;
}

.estadoContainer {
  border-radius: 1rem;
  width: 24.4rem;

  transition: 0.25s ease;
  background-color: var(--red);

  & .estado {
    width: 100%;
    height: 100%;
    padding: 0.5rem;
    border-radius: 1rem;

    pointer-events: none;

    & .jornadaTable {
      th {
        color: white;
        font-size: 1.1rem;
        font-weight: 500;

        .dx-button-content {
          padding: 0;
        }
      }

      td {
        font-size: 1.5rem;
        border-radius: 0.7rem;
        font-weight: 700;
        text-align: center;

        & .jornadaContainer {
          border-radius: 0.7rem;
          background-color: white;
          margin-bottom: 0rem;
        }
      }
    }
  }
}

.estadoContainer:hover {
  cursor: pointer;
  filter: brightness(85%);
  -webkit-filter: brightness(85%);
}

.estadoContainer:active {
  filter: brightness(75%);
  -webkit-filter: brightness(75%);
}

.bg-red:not(:hover, :focus) {
  background-color: var(--red) !important;
}

