.listaEventos {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.4rem;

  ul,
  li {
    list-style: none;
    padding: 0;
    text-align: left;
  }

  & .evento {
    margin: 0;
    border-radius: 12px;
    position: relative;
    height: min-content;
  }

  & li {
    padding-bottom: 2.5rem;
    border-left: 2px solid var(--primary);
    position: relative;
    padding-top: 2px;
    padding-left: 20px;
    margin-left: 10px;

    &:last-child {
      padding-bottom: 5px;
    }

    &:before {
      color: var(--primary);
      width: 15px;
      height: 15px;
      background: white;
      padding: 0.75rem;
      outline: 2px solid var(--primary);
      border-radius: 50%;
      position: absolute;
      left: -11px;
      top: 0px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  & .estado {
    color: #2a2839;
    font-weight: 500;
  }

  & span {
    color: var(--secondary);
    font-family: "Helvetica Neue", "Segoe UI", Helvetica, Verdana, sans-serif;
    font-size: 1rem;
  }
}

.eliminarJornada {
  border-radius: 1rem;
}

.eliminarJornada:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.5);
  filter: brightness(80%);
  color: var(--primary);
}

.dxButtonDropDownEventoCalendario {
  .dx-button-content {
    background-color: #5cb85c;
    color: white;
    padding: 8.5px 18px !important;
    border-radius: 4px;

    &:hover {
      background-color: #46a046;
    }
  }

  .dx-buttongroup-item-content:not(.dx-item-selected) span {
    font-weight: 400;
  }
}
