.resumenCard {
    outline: 2px solid var(--cardColor);
    color: var(--cardColor);
    
    i {
        color: var(--cardColor) !important;
        pointer-events: none;
    }

    .resumenCard-title {
        font-size: 15px;
        font-weight: 600;
        color: #999;
    }

    .resumenCard-body {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        font-size: 25px;
        font-weight: 700;
        color: #646464;
    }
}