.DataGrid_Nominas {
  .chipContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    zoom: 0.9;

    >i {
      color: inherit !important;
    }
  }

  .chipContainer:hover {
    cursor: pointer;
    zoom: 1;
  }

  .chip {
    color: white;
    font-weight: 700;
    padding: 5px 15px;
  }

  .chip.success {
    background-color: var(--success);
  }

  .chip.secondary {
    background-color: var(--secondary);
  }

  .chip.info {
    background-color: var(--info);
  }

  .chip.warning {
    background-color: var(--warning);
  }

  .chip.danger {
    background-color: var(--danger);
  }

  .chip.disabled {
    background-color: var(--secondary);
    opacity: 0.5;
  }

  i {
    font-size: 1.5rem;
  }
}