.ValidacionNominas {
    .filtros, .dx-box {
        display: flex;
        flex-direction: column;
        gap: .5rem;
    }
    
    .filtros {
        height: 54rem;
    }

    .dx-list-item:hover {
        background-color: rgba(0, 0, 0, 0.04);
    }

    .listItem {

        .nombreLista {
            font-size: 1.1rem;
            font-weight: bold;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    
        .chipEstado {
            background-color: var(--secondary);
            width: fit-content;
            padding: .1rem .4rem;
            border-radius: .3rem;
            color: white;
            font-weight: 700;
        }
    }

    .colsTransition {
        transition: .25s ease, visibility 0.25s ease;
    }

    .col-Hidden {
        translate: -100%;
    }

    .col-Left {
        translate: 0%;
    }

    .col-Right {
        right: 0;
    }
    
    .lottieDiv {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        display: flex;
        flex-wrap: wrap;
        align-content: center;
        justify-content: center;

        .lottieContainer {
            width: 40rem;
            background-color: white;
            border-radius: 2.5rem;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
            border: 1px solid rgba(0, 0, 0, 0.3);
            

            span {
                font-weight: 700;
                font-size: 1.5rem;
                padding: 1rem;
                width: 100%;
                display: block;
                text-align: center;
            }
        }
    }
}