.dx-gallery .image {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.dx-gallery .dx-gallery-item-image{
  max-height:100%;
}

.dx-gallery.topNavButtons .dx-gallery-nav-button-prev,
.dx-gallery.topNavButtons .dx-gallery-nav-button-next {
  height: 50px;
  width: 50px;

  display: flex;
  align-items: center;
  justify-content: center;

  &::after {
    width: 100%;
    border-radius: 25px;
  }
  &::before {
    color: inherit;
    margin-top: 0px;
    margin-left: 0px;
    position: initial;
    font-size: 2rem;
  }
  &.dx-state-hover::before {
    color: white;
  }
}

.dx-gallery.outIndicators .dx-gallery-wrapper {
  & .dx-gallery-container {
    height: calc(100% - 30px);
  }
}
