.DataGrid_Nominas_RD {
    .sticky-grid-row-header-right {
        position: sticky;
        left: 30px;
        z-index: 1;
        background-color: white;
        overflow: visible !important;
        > div {
            padding: 7px;
            height: 100%;
            border-right: 2px solid #ddd !important;
            right: -1px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .sticky-grid-row-header-left {
        position: sticky;
        z-index: 1;
        background-color: white;
        overflow: visible !important;
        > div {
            padding: 7px;
            height: 100%;
            border-left: 2px solid #ddd !important;
            left: -1px;
        }
    }
    .botonGenerarSAP{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;

        > i {
        color: inherit !important;
        }

    }
    .botonGenerarSAP:hover {
        color: var(--primary) !important;
        cursor: pointer;
    }

    i {
        font-size: 1.5rem;
    }
}