#dxRadioGroup_tipoEntidad_EstructuraOrganizativa {
  .dx-item-content {
    text-align: center;
  }
}

#dxPopup_configEntidad {
  .dx-buttongroup-item .dx-button-content {
    padding-left: 0px;
  }
}

#dxMultiView_pedidos_estructuraOrg {
  .dx-multiview-item-content {
    padding: 0px;
    padding-top: 25px;
    padding-left: 5px;
  }
  .dx-multiview-wrapper {
    padding-top: 0px !important;
  }

  .disabled {
    opacity: 0.5;
  }
}

.dx-datagrid-rowsview .dx-data-row .dx-cell-modified.no-CellEdit-Highlight .dx-highlight-outline::after {
  border: none;
}

.no-CellEdit-Highlight {
  .dx-highlight-outline.dx-pointer-events-target {
    padding: 0px;
  }
}

#dxDataGrid_tblPrenda_precios .dx-command-select {
  width: 35px !important;
  min-width: 35px !important;
  max-width: 35px !important;
}

.dxList_no_loadPanel {
  .dx-scrollview-scrollbottom {
    display: none !important;
  }
}

#dxForm_entidad .dx-item.dx-tab {
  box-shadow: inset 0px -2px 0px -0px var(--secondary);
}
#dxForm_entidad .dx-item.dx-tab.dx-tab-selected {
  box-shadow: inset 0px -2px 0px -0px var(--primary) !important;
}

.invalidCell {
  input[type="text"] {
    color: red !important;
    font-size: 15px;
    text-align: center;
    padding: 5px;
    font-weight: 600;
  }
}

.color-gray {
  input[type="text"] {
    color: #5e5e5e !important;
    font-size: 15px;
    text-align: center;
    padding: 5px;
    font-weight: 600;
  }
}
.color-gray.dx-textbox {
  margin-left: 4px !important;
}

.color-white {
  input[type="text"] {
    color: #ffffff !important;
    text-align: center;
    font-size: 15px;
    padding: 5px;
    font-weight: 600;
  }
}

.color-white.dx-textbox {
  margin-left: 4px !important;
}

.color_preview {
  height: 25px;
  width: 25px;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

::ng-deep #dxDataGrid_gestionCorreosAlbaranes .dx-datagrid-rowsview .dx-texteditor-input {
  min-height: 23px !important;
}

.pr-10px {
  padding-right: 10px !important;
}

.list_denominacion_container {
  flex: 1;
  font-size: 16px;
  padding-left: 10px;
}

.h-25px {
  height: 25px;
}

.container_cellRender_elemTrans {
  border-right: 1px double gainsboro;
  flex: 50%;
}

.cellRender_elemTrans {
  height: 20px;
  width: 25px;
  cursor: pointer;
}

.flex-50 {
  flex: 50%;
}

.flexBasis-50 {
  flex-basis: 50;
}

.color-secondary {
  color: var(--secondary);
}

.dxTreeView_deno_compaEnti {
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 8px;
}

#dxSwitch_inventarioPorEntidad {
  .dx-switch-off {
    color: #5e5e5e;
  }

  .dx-switch-off-value .dx-switch-handle:before {
    color: #edb637;
  }
}

.dxTabPanel_Entidad {
  .dx-tabs-item-badge.dx-badge {
    background-color: #d9534f;
    border-radius: 50%;
    width: 18px;
    height: 18px;
  }
}

.deletePrenda_alertList {
  display: flex;
  padding-left: 15px;
  text-align: center;

  p {
    width: fit-content;
    justify-content: center;
    align-items: center;
    text-align: start;
  }
}

.deletePrenda_alertList ~ p {
  margin-bottom: 0px !important;
}
.deletePrenda_alertList p {
  margin-bottom: 0px !important;
}

.importantText {
  text-align: justify;
  font-size: 0.85rem;
  font-weight: bold;
  padding-top: 10px;
  max-width: 285px;
}

.importantText span:first-of-type {
  color: var(--red);
}

.deletePrenda_alertList::first-line {
  line-height: 15px;
}

.no-disabled.dx-button.dx-state-disabled {
  opacity: 1;
  background-color: rgb(245, 245, 245);
  & .dx-button-text {
    opacity: 1;
  }
}

.EditCellComponent_excluir {
  width: 25px;
  height: 25px;
  .dx-icon {
    font-size: inherit;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
