@font-face {
  font-family: "menu_icons";
  src: url("./fonts/icons.eot?5kiojs");
  src: url("./fonts/icons.eot?5kiojs#iefix") format("embedded-opentype"),
    url("./fonts/icons.ttf?5kiojs") format("truetype"), url("./fonts/icons.woff?5kiojs") format("woff"),
    url("./fonts/icons.svg?5kiojs#menu_icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="menu_icons_"],
[class*=" menu_icons_"] {
  font-family: "menu_icons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: var(--secondary);
}

.has-active-child [class^="menu_icons_"],
.has-active-child [class*=" menu_icons_"],
.active [class^="menu_icons_"],
.active [class*=" menu_icons_"] {
  color: var(--white);
}

.menu_icons_Account:before {
  content: "\e931";
}
.menu_icons_Logout:before {
  content: "\e932";
}

.menu_icons_ThreeDots {
  color: white;
  padding: 5px 0px;
  &:before {
    font-size: 18px;
    content: "\e933";
