#opciones_container {
  position: absolute;
  right: 0;
  height: 100%;
  width: 400px;
  padding: 20px;
  transform: translate(100%);
  transition: all 0.2s;
  box-shadow: 0px 2px 6px 0px #777;
  background: var(--white);
  z-index: 7;

  &:not(.opened){
    width: 0px;
    padding: 0px;
  }

  &.opened {
    transform: translate(0);
    right: 0;
  }
}

#opciones_title {
  position: absolute;
  right: -19px;
  top: -15px;
  height: 62px;
  width: 399px;
  box-shadow: 0px -5px 5px 0px #777;
  padding-left: 15px;

  & span {
    flex: 1;
    font-size: 20px;
  }

  #opciones_close {
    font-size: 30px;
    width: 60px;
    height: 100%;
    &.opened {
      background: var(--primary);
      color: white !important;
    }
  }

  &:hover {
    #opciones_close {
      background: var(--primary);
      color: white !important;
    }
  }
}

@media all and (max-width: 991px) {
  #opciones_title {
    position: inherit;
    width: 100%;
    box-shadow: 0px 0px 3px 0px #777;
    height: 50px;
  }
}
