#MonitorSat
{
    #CircularGauge_selector {
        width: 100%;
        height: 100%;
        min-height: 75px;

        svg
        {
            height:105%;
        }
      }
    
      #CircularGauge_recursos {
        width: 100%;
        height: 100%;
        min-height: 75px;
      }
    
      .row-1 {
        height: 16.5rem;
        min-height: 150px;
      }
    
      .row-2 {
        .card{
            min-height:350px;
        }
    
        #dxDataGrid_incidencias
        {
          min-height:300px;
        }
      }
    
      .row-2-mobile
      {
        display: block;
        height:105px;
        min-height: 105px;
      }

    .page-2
    {
        .dx-scrollable-content, .dx-scrollview-content
        {
            padding:0px;
        }
    }

    .page-2 > div > .dx-scrollable-container
    {
        padding:5px;
    }
    
    .page-1
    {
        .dx-scrollable-content, .dx-scrollview-content
        {
            padding:7px;
            padding-top:0px;
			padding-bottom:0px;
        }
    }

    #dxScrollView_multiView.page-2
    {
        .dx-scrollable-content:first-child{
            height:100%;
        }
        .dx-scrollview-content
        {
            height:100%;
            display: flex;
            flex-direction: column;
        }
    }

    .vMovil
    {
        .dx-scrollable-content, .dx-scrollview-content
        {
            padding:1px;
        }

        #dxScrollView_multiView
        {
            .dx-scrollable-content, .dx-scrollview-content
            {
               padding:0;
            }
        }
    }

    .card .dx-scrollable-content, .card .dx-scrollview-content
    {
        padding:0px;
    }

    .border-width-2{
        border-width:2px !important;
    }

    .card-header
    {
        padding-right: 1.25rem;
    }
   
    #graficasConsumos 
    {
        .dx-item-content.dx-accordion-item-body
        {
            padding-left:5px;
            padding-right:0px;
        }
        .dx-accordion-item-title
        {
            padding-top: 0;
        }
    }

    #dxDataGrid_incidencias
    {
        .dx-datagrid-filter-row td:nth-last-child(2)
        {
            display: none;// para ocultar filtro bsqueda grupos mquinas en grid incidencias
        }

        @media (max-width: 1199px) {
            td{
                padding:5px;
            }
    
            input{
                min-height:0px;
                padding:0px;
            }
            .dx-layout-manager .dx-field-item:not(.dx-first-row)
            {
                padding-top: 0px;
            }

            .dx-adaptive-detail-row td
            {
                padding-left:15px;
            }
        }
        @media (min-width: 1200px) {
            .dx-master-detail-row td{
                padding:12px;
            }
        }
        .dx-command-expand.dx-datagrid-group-space
        {
           width:1px !important;
           min-width: 1px;
        }
    
        .dx-command-edit.dx-command-edit-with-icons
        {
            width:25px !important;
            min-width: 25px;
        }
      
        .dx-datagrid-rowsview .dx-adaptive-detail-row .dx-adaptive-item-text
        {
            padding:0px;
        }
        .container_spanCentrado
        {
            padding-left: 0px !important;
        }

        .dx-layout-manager .dx-field-item:not(.dx-last-col)
        {
            padding-right:0px;
        }
    }
    .he-auto{
        height:auto;
    }

    @media (max-width: 767.98px) {
        padding-bottom: 20px;
    }
}

#dxTileView_documentos
{
    .dx-empty-message {
        width:95px;
    }
}

//#region Popups
@media (max-width: 767.98px) {
    #dxDataGrid_recambios_monitorSat, #dxDataGrid_parteTrabajo_monitorSat
    {
        td{
            padding:5px;
        }

        .dx-datagrid-rowsview .dx-adaptive-detail-row .dx-adaptive-item-text
        {
            padding:1px;
            padding-top:3px;
        }

        .dx-adaptive-detail-row td
        {
            padding-left:15px;
        }

        input{
            min-height:0px;
            padding:0px;
        }

        .dx-layout-manager .dx-field-item:not(.dx-first-row)
        {
            padding-top: 0px;
        }
    }            
}

#form-incidencias .dx-scrollable-content, #form-parteTrabajo .dx-scrollable-content{
   width:100%;
}

@media (max-width: 1199px) {
    #form-incidencias .dx-fileuploader-input{
        display: none;
    }
}

#form-incidencias, #form-parteTrabajo > div{
    padding:15px;
}

.tblParteTrabajo_tecnicos
{
    padding-top:10px;
}

.parteTrabajo_tabPanel .dx-multiview-wrapper
{
    padding-top:0px;
}
.parteTrabajo_tabPanel .dx-item-content.dx-multiview-item-content
{
    padding-top:17.5px;
    padding-left:6px
}
//#endregion

