#Lavanderia {
    .dx-tabpanel-container>.dx-multiview-wrapper {
        padding-top: 0px !important;
    }

    .dxTabPanel_correos_item {
        padding-top: 0px !important;

        .dx-tab-selected {
            box-shadow: inset 0px -1px 0px 0px var(--primary) !important;
        }

        .dx-item.dx-tab:not(.dx-tab-selected) {
            box-shadow: inset 0px -1px 0px 0px var(--secondary) !important;
        }
    }
}

.buttonSeleccionarUbicacion {
    height: 100%;
    display: flex;
    align-items: flex-end;
}