#ControlEnvios 
{
	.dx-datagrid table > tbody
	{
		background-color:white;
	}
	& .icon_color_hover:hover
	{
		color:rgb(158,158,158) !important;
	}

	& .iconos_svg
	{
		height:25px;
		width:25px;
		cursor:pointer;
		font-size:25px;
	}

	.formContainer.scrollbar-container
	{
		padding-top:8px !important;
	}

}
	#dxButton_guardarEnvio_controlEnvios{
		margin-right: 15px;
	}

	#popupEnvio_formContainer
	{
		padding-top:0px !important;
		
		.dx-scrollable-content{
			padding-right:15px;
		}
		#Form_envio
		{
			//padding:5px !important;
			padding:0px !important;
		}
	}
  
	 #dxTileView_fotos_articuloEnvio
	{
		& .image
		{
			height:100%;
			width:100%;
			background-size:contain;
			background-position:50%;
			background-repeat:no-repeat;
		}

		& .dx-scrollview-content
		{
			height: 100% !important;
		}

		& .icon_color_hover:hover
		{
			color:rgb(158,158,158) !important;
		}

		& .iconos_svg
		{
			height:25px;
			width:25px;
			cursor:pointer;
			font-size:25px;
		}

		& #contenedorImg {
			height: 100%;
			& > .image {
			  height: 100%;
			  width: 100%;
			  background-position: center;
			  background-size: cover;
			}
		  }
		
		  /*Mostrar las imagenes en la galera*/
		  & .image,
		  .image {
			height: 100%;
			width: 100%;
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
		  }
		
		  & .dx-item.dx-state-hover,
		  & .dx-item.dx-state-active {
			& .dxToolbar_opciones {
			  display: block;
			  width: 70px !important;
			}
		  }
	}

	.chip-noColor {
		display: inline-block;
		padding: 0 8px;
		border-radius: 25px;
	}

	.controlEnvios_tooltipComentarios{
		white-space: normal;
		overflow-wrap: break-word;
		overflow-y: auto;
	}

#Form_envio 
{
	.dx-scrollable-content
	{
		padding-right:0px;
	}
	& .dx-layout-manager .dx-tabpanel .dx-multiview-item-content
	{
		padding: 10px 0px 0px 0px ;
	}

	& .dx-tab-text
	{
		font-size:1.4rem;
	}

	& #list_tipoDocumento .dx-item-content.dx-box-item-content > div > div
	{
		font-size:20px;
	}

	& #list_tipoDocumento {
		& .dx-checkbox-icon {
			width: 27px;
			height: 27px;
		}

		& .dx-checkbox-icon:before {
			font-size:20px;
			left:44%;
			top:48%;
		}
	}

	& #dxGallery_documento{
		& .dx-gallery-nav-button-next, .dx-gallery-nav-button-prev
		{
			width:0px !important;
		}
		& .image
		{
			height:100%;
			width:100%;
			background-size:contain;
			background-position:50%;
			background-repeat:no-repeat;
		}
	}

	& #dxTabPanel_documentacion
	{ 
		& .dx-tabs-wrapper{
			display:none !important;
		}
		& .dx-tabpanel-container{
			padding-top:0px !important;
		}
		& .dx-multiview-wrapper, .dx-multiview-item-content{
			padding-top: 0px !important;
		}
	}
	& .icon_color_hover:hover
	{
		color:rgb(158,158,158) !important;
	}

	& .iconos_svg
	{
		height:25px;
		width:25px;
		cursor:pointer;
		font-size:25px;
	}
}

 
#TabDocumentos_Padre_controlEnvios, #TabPackingList_Padre_controlEnvios {
    filter:blur(3px);
}

.dx-state-invisible + #TabDocumentos_Padre_controlEnvios,.dx-state-invisible + #TabPackingList_Padre_controlEnvios 
{
    filter:blur(0px) !important;
}

.disabledComponent {
	opacity: 20%;
	cursor: not-allowed !important;
}