#CategoriasMaquinas
{
    .dx-Cell_Editable .dx-datagrid-text-content{
        padding-right: 0px;
    }

    .dx-Cell_Editable .dx-datagrid-summary-item.dx-datagrid-text-content{
        padding-right: 0px;
    }

    .dx-scrollable-container
    {
        padding-right:1px;
    }

    .dx-scrollable-content:first-child, .dx-scrollview-content:first-child
    {
        padding:0;
    }


    .dx-freespace-row {  
        height: 0px !important;  
    } 

    .dx-master-detail-cell
    {
        padding:7px 20px 7px 0px;

    }

    .dx-datagrid-rowsview .dx-scrollable-content{
        box-sizing: content-box;
    }

    #dxScrollView
    {
        .dx-scrollview-content
        {
            height:100%;
        }
    }

    #dxScrollView > div > div > div.dx-scrollable-content
    {
        height:100%;
        padding-top: 0px !important;
        padding-bottom: 0px !important;
    }
    #dxScrollView.vMovil > div > div > div.dx-scrollable-content
    {
        padding-top: 1px !important;
        padding-bottom: 1px !important;
        height: 1350px;
    }

    .vMovil#dxScrollView
    {
        margin-right:-12px !important;

        .dx-scrollable-content:first-child, .dx-scrollview-content:first-child
        {
            padding:0;
        }
    }
  
    .vMovil
    {
        #BoxPrincipal
        {
            padding-right:15px;
        }

        .dx-scrollable-container
        {
            padding-right:1px;
        }

        .dx-datagrid-rowsview .dx-scrollable-container
        {
            overflow-x:hidden;

            .dx-scrollbar-horizontal{
                visibility: hidden;
            }
        }
    }
 
}
