.dx-treeview {
  [aria-level="1"]:not(.dx-state-selected):not(.dx-state-focused) > .dx-treeview-item {
    background-color: rgb(245, 245, 245);
  }

  #dxTreeView_compaEnti [aria-level="2"] > .dx-treeview-item {
    height: 40px;
    display: flex;
    align-items: center;
  }

  #dxTreeView_compaEnti [aria-level="2"]:not(:last-child) > .dx-treeview-item {
    border-bottom: 1px double gainsboro;
  }

  #dxTreeView_compaEnti [aria-level="2"] > .dx-treeview-item > .dx-treeview-item-content {
    width: 100%;
  }

  .dx-treeview-node {
    padding-left: 20px;
  }

  .dx-treeview-toggle-item-visibility::before {
    content: "\f016";
    font-size: 16px;
    line-height: 35px;
  }

  .dx-treeview-toggle-item-visibility.dx-treeview-toggle-item-visibility-opened::before {
    content: "\f014";
  }

  .dx-treeview-node.dx-state-selected,.dx-treeview-node.dx-state-focused {
    & > .dx-item.dx-treeview-item {
      background: var(--primary);
    
      & .dx-item-content{
        color: white;
      }
    }
  }
}
