#DashboardLecturaLavadoras {
    .dx-scrollview-content
    {
        padding:0px;
    }

    .dx-item-content.dx-multiview-item-content
    {
        padding:15px;
    }

    .dx-item.dx-multiview-item.dx-item-selected
    {
        display: contents;
    }

    .header-border-right{
        border-right:1px solid rgba(151, 109, 14, 0.125);
    }

    .card-header{
        display:flex !important;
        height:5.3rem !important;

        min-height:91px;
        padding-right:0px;

        &.no_enUso
        {
            height:5.3rem !important;
           min-height:88px;
        }
    }

    .card-header-title
    {
        padding: 0px 0.625rem;
        line-height:25px !important;
    }

    .card-footer
    {
        padding: 0.7rem 1.25rem;
     }

    .cardFooter-border{
        padding-left: 1.25rem;
        padding-right:1.25rem;
    }

    .card-footer .timeText
    {
        font-size:2.8rem;
    }

    .gauge_labelKilos{ 
       bottom:-20px;
        & div
            {
                font-size:1.9rem;
            }
        & div > span
            {
                font-size:1.2rem;
            }
    }
    .text_pendiente
    {
        bottom:0px;
    }

    @media (max-height: 768px) {
        .row-1 {
             height: 15vh;
          }
          
          .font-size-iconLavadora
          {
              font-size:8em;
          }

        .gauge_labelKilos{
            bottom:-5px;
        }
        .card-body .widget-content
        {
            padding-bottom: 0.5rem !important;
        }

        .text_pendiente>div{
            font-size:0.8rem;
        }
    }

    @media (min-height:769px) and (max-height: 840px) {
        .row-1 {
            height: 16vh;
         }

         .font-size-iconLavadora
         {
             font-size:9em;
         }
        .text_pendiente>div{
            font-size:1.2rem;
        }

        .card-body .widget-content
        {
            padding-bottom: 1.5rem !important;
        }
    }

    @media (min-height: 841px) {
        .row-1 {
            height: 17vh;
         }
         .font-size-iconLavadora
         {
             font-size:9em;
         }
        .text_pendiente>div{
            font-size:1.2rem;
        }

        .card-body .widget-content
        {
            padding-bottom: 1.5rem !important;
        }
    }

    @media (max-width: 767px) {
        .dx-scrollable-container
        {
            height:calc(100% - 44px) !important;
        }
    }

    @media (min-width: 1200px) {
        .col-xl-5ths {
            flex:0 0 20% !important;
            width: 20%;
            float: left;
        }
    }

    @media (min-width: 1200px) and (max-width: 1880px) {
        .gauge_labelKilos{
            & div 
            {
                font-size:1.5rem;
            }
            & div > span
            {
                font-size:0.9rem;
            }
        }
        .text_pendiente>div
        {
            font-size:1rem;
        }

        .card-footer .timeText
        {
            font-size:2rem;
        }

        .text-font-capacidadLavadora
        {
            font-size:2.5rem !important;
        }
    }

    @media (min-width: 1881px) {
        .gauge_labelKilos{
            & div 
            {
                font-size:2rem;
            }
            & div > span
            {
                font-size:1.4rem;
            }
        }
        .text_pendiente>div
        {
            font-size:1.5rem;
        }
           
        .card-footer .timeText
        {
            font-size:2.8rem;
        }
    }

    .lineHeight-normal{
        line-height:normal;
    }

    .border-3 {
        border-width:3px !important;
    }

    .codigoLav-font
    {
        font-size:3.5rem;
    }

    .text-font-capacidadLavadora
    {
        font-size:3.4rem;
    }

    .he-fitContent
    {
        height:fit-content;
    }

    .dxCircularGauge {
        width: 100%;
        height: 100%;
    }

    .minLabel
    {
        line-height: 1.6;
        align-items: flex-end;
    }
}
