.flex-1{
    flex: 1;
}

.border-0{
    border: 0px;
}

.contentPosition_start{
    //display
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    //tamao
    width: 100%;
    height: 100%;
    //padding
    padding: 5px;
}

.contentPosition_center{
    //display
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contentPosition_end{
    //display
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.contentPosition_spaceBetween{
    //display
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.contentPosition_spaceAround{
    //display
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.border-radius{
    //borde
    border-radius: 15px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
}

.shadow{
    //sombra 
    box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.50);
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.50);
    -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.50);
}

.container_formRespuestaEncuesta {
    //borde
    border-radius: 15px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    //fondo
    background-color: #ffffff;
    //padding
    padding: 15px;
    //tamao
    width: 100%;
    //altura
    min-height: 150px;
    //display
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.font-weight-semiBold{
    font-weight: 600;
}
