.popup-solicitud-alta-gestoria {
  .dx-popup-content {
    padding: 0;

    .dx-multiview-item-content {
      padding: 20px;
    }

    .container-NAF {
      .dx-field-item-content {
        height: 100% !important;

        .dx-form-group.dx-group-colcount-1 {
          height: 100% !important;
          .dx-form-group-content {
            height: 100% !important;
            .dx-layout-manager.dx-widget {
              height: 100% !important;
              .dx-box-flex.dx-box.dx-widget.dx-visibility-change-handler.dx-collection {
                justify-content: space-between !important;

                div.dx-item.dx-box-item {
                  flex-grow: inherit !important;
                }
              }
            }
          }
        }
      }
    }

    .alert-iban-no-solicitado {
      background-color: rgba(255, 255, 255, 0.75);
    }

    .validacion-tabbed-documentos {
      .dx-item.dx-tab {
        font-size: 16px;
        font-weight: 500;
        padding-bottom: 0px;
        padding-top: 0px;
      }

      .dx-item-content.dx-multiview-item-content,
      .dx-multiview-wrapper {
        padding: 0;
      }

      .dx-field-item-content.dx-field-item-content-location-right,
      .dx-collection.dx-multiview.dx-swipeable.dx-tabpanel.dx-visibility-change-handler.dx-widget,
      .dx-layout-manager.dx-widget,
      .dx-form-group,
      .dx-form-group-content,
      .dx-first-row.dx-first-col.dx-last-col.dx-last-row.dx-field-item.dx-field-item-tab.dx-col-0.dx-field-item-optional.dx-flex-layout {
        height: 100% !important;
      }
    }
  }

  .dx-button-mode-outlined.dx-button-success .dx-icon {
    color: #4cae4c !important;
  }
  .dx-button-mode-outlined.dx-button-danger .dx-icon {
    color: #d43f3a !important;
  }
}
