$day-border-radius: 10px;

#YearCalendarPolarier {
    width: 100%;
    height: 100%;

    & .calendar {
        min-width: 1570px;
    }

    & .calendar-header {
        border: none;
    }

    & .month {
        width: 250px;
        border-spacing: 0 5px;

        & tr:nth-child(2n) th {
            border: none !important;
        }

        & .day:not(.range) .day-content:hover {
            border-radius: $day-border-radius;
        }
    }

    & .month-title {
        border: none !important;
        text-align: left !important;
    }

    & .month-container {
        height: 250px !important;
        width: calc(100% / 6) !important;
    }

    & .day-disabled {
        opacity: 0.5;
        color: inherit;

        & :hover {
            cursor: not-allowed;
        }
    }

    .day-disabled:hover {
        cursor: not-allowed;
    }

    & .day-content {
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: normal;
        border-radius: 0;
        padding: 0 !important;

        & .today-mark {
            width: 15px;
            border-bottom: 2px solid var(--info);
        }

    }

    & .day:has(.day-disabled).day-content:hover {
        background: none !important;
    }

    & .day>div {
        width: 36px;
        height: 22.5px;
        font-size: 12.5px;
    }

    & .day:first-child,
    .range-start .day-content,
    .range:first-child .day-content,
    .evento-start,
    .day:first-child .selected,
    .selected-start {
        border-top-left-radius: $day-border-radius;
        border-bottom-left-radius: $day-border-radius;
    }

    & .day:last-child,
    .range-end .day-content,
    .range:last-child .day-content,
    .evento-end,
    .day:last-child .selected,
    .selected-end {
        border-top-right-radius: $day-border-radius;
        border-bottom-right-radius: $day-border-radius;
    }

    & .selected {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        // background-color: rgba(247, 184, 36, 0.5);

        border: 3px;

        border-top: solid var(--primary);
        border-bottom: solid var(--primary);
    }

    & .selected-start,
    .day:first-child .selected {
        border-left: solid var(--primary);
    }

    & .selected-end,
    .day:last-child .selected {
        border-right: solid var(--primary);
    }

    & .diaCierre {
        position: relative;
        background-color: #f5f5f5;
    }

    & .diaCierre::before,
    & .diaCierre::after {
        content: "";
        width: 65%;
        position: absolute;
        right: 6px;
        top: 46%;
        border-bottom: 2px solid var(--danger);
        z-index: 2;
    }

    & .diaCierre::before {
        -webkit-transform: skewY(-22.5deg);
        transform: skewY(-22.5deg);
    }

    & .diaCierre::after {
        -webkit-transform: skewY(22.5deg);
        transform: skewY(22.5deg);
    }

    & .stripe {
        color: #1d2429;
        background: repeating-linear-gradient(-45deg,
                #ffffff,
                #ffffff 2.5px,
                #ffffff00 2.5px,
                #ffffff00 5px);
    }
}