.form-datos-personales {
  .container-NAF {
    .dx-field-item-content {
      height: 100% !important;

      .dx-form-group.dx-group-colcount-1 {
        height: 100% !important;
        .dx-form-group-content {
          height: 100% !important;
          .dx-layout-manager.dx-widget {
            height: 100% !important;
            .dx-box-flex.dx-box.dx-widget.dx-visibility-change-handler.dx-collection {
              justify-content: space-between !important;

              div.dx-item.dx-box-item {
                flex-grow: inherit !important;
              }
            }
          }
        }
      }
    }

    .dx-field-item-label-content {
      width: auto !important;
    }
  }

  .alert-iban-no-solicitado {
    background-color: rgba(255, 255, 255, 0.75);
  }

  .container-Adjuntos {
    .dx-field-item-content {
      height: 100% !important;
      .dx-form-group {
        height: 100% !important;
        .dx-form-group-content {
          height: 100% !important;
        }
      }
    }
  }

  .borde-gestoria {
    position: relative;

    .dx-field-item-label.dx-field-item-label-location-top {
      position: absolute;
      top: 0px;
      left: 50%;
      width: 130px;
      text-align: center;
      transform: translateX(-50%);
      background-color: white;
    }

    .dx-texteditor {
      border-radius: 10px;
    }
  }

  .selector-nacionalidad {
    .dx-lookup-arrow {
      display: none;
    }

    .align-items-center.countryContainer.d-flex {
      width: 100% !important;
      padding: 0 9px;
    }
  }
}
