.yearCalendar_opacity{
    background: rgba(238,182,55,0.7) !important;
}

.errorDate{
  background-color: var(--red) !important;
}

.calendar .calendar-header {
    .year-title
    {
        vertical-align: middle;
    }
    .prev span{
        display:block;
        padding-bottom: 5px;
    }
}

.calendar table.month td.day .day-content{
  padding: 4px 6px !important;
}

div{
  /*  Estilos calendario year-calendar    */
  & .calendar {
    color: #484848;
    font-size: 12px;
    font-family: "Helvetica Neue", "Segoe UI", "Helvetica", "Verdana", "sans-serif";
    padding-top: 0px;
    padding-bottom: 0px;
    height: 99%;
    overflow: hidden;
 
    .svg_warning{
      fill:rgb(255, 35, 0) !important;
    }
  }

  & .year-title {
    font-weight: 600 !important;
  }

  & .calendar .day > div {
    font-size: 12px;
    font-family: "Roboto Medium", "Helvetica", "Droid Sans", "Tahoma", "Geneva", "sans-serif";
    width: 30px;
    height: 22px;
  }

  & .month {
    border-collapse: separate;
  }

  & .calendar thead:after{
    line-height: 1px;
    content: "_";
    color: transparent;
    display: block;
  }

  & .month-title {
    color: #999999;
    font-family: "Roboto Medium", "Helvetica", "Droid Sans", "Tahoma", "Geneva", "sans-serif";
    font-size: 16.1px;
    padding: 2px 0px !important;
    border: 1px #d9d9d9 solid !important;
    border-radius: 4px !important;
  }

  & .month tr:nth-child(2n) th {
    padding: 5px 5px 2px 0px !important;
    text-transform: uppercase;
    color: #999999;
    font-size: 12px;
    border-bottom: 1px solid #999999;
  }

  & .calendar-header {
    border-radius: 4px;
  }

  & .calendar .months-container .month-container {
    height: 200px !important;
  }

  & .months-container {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
    height: 100%;
    width: 100%;
    display: inline-block !important;
  }

  div.calendar table.month .day-start .day-content
  {
    border-radius: 15px 0px 0px 15px;
  }
  
  .singleDay_betweenDates{
    border-radius: 0px !important;
    background-color: rgba(238,182,55,0.7) !important;
  }

  .roundedDay{
    border-radius:  15px !important;
  }
}

  #dxPopup_titleBar{
    border-bottom: 1px var(--primary) solid;
  }

  .title-dxpopup-fecha{
        padding-top: 0px !important;
        padding-bottom: 0px !important;
  }

  .noBackground{
    background: none !important;
  }

  .disabledDay {
    color: #dddddd;
  }