#Inventarios {
  .dx-trackbar.dx-widget.dx-progressbar,
  #porcentajeValue {
    color: white;
  }

  .dx-progressbar-container {
    background-color: #b7c2c7;
  }

  #subInvTitulo {
    display: inline;
    width: 100%;
    text-align: center;
    font-weight: bold;
  }

  .contenedorTituloInv {
    display: grid;
    grid-auto-columns: 5fr 2fr;
    grid-auto-flow: column;
    border-bottom: 3px solid #ebb342;
    padding-top: 5px;
  }

  #nombreInventarioContainer {
    white-space: nowrap;
    font-size: 29px;
    color: #6d6c6c;
  }

  #porcentajeContainer {
    height: 80%;
    align-self: center;
    background-color: #b7c2c7;
    border-radius: 3px;
  }

  #porcentajeInventarioText {
    font-family: "Helvetica Neue", "Segoe UI", Helvetica, Verdana, sans-serif;
    font-size: 20px;
    background-color: transparent !important;
    padding-left: 10px;
  }

  #porcentajeValue {
    height: 100%;
    display: inline-flex;
    align-items: center;
  }

  #tit_nombreInventario {
    padding-left: 12px;
    align-self: center;
    max-width: 60%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: inline-block;
  }

  #tit_fechaInventario {
    white-space: nowrap;
    vertical-align: top;
  }

  .innerGrop_compaEnti .dx-form-group-with-caption {
    padding-left: 0px !important;
  }
}
#dxDataGrid_tblPrendasNInventario .dx-freespace-row,
#dxDataGrid_addPrendasInventario .dx-freespace-row {  
  height: 0px !important;  
} 
