#displayReportConflictosLeft_container {
  position: absolute;
  display: flex;
  z-index: 11;
  left: 0;
  top: 0;
  height: 100%;
  width: 55%;
  transform: translate(-100%);
  transition: all 0.2s;
  background-color: rgba(54, 54, 53, 0.445);

  #reportViewer_close {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    & :before {
      color: rgb(54, 54, 53) !important;
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.5);

      & :before {
        color: white !important;
      }
    }
  }

  .reportViewer {
    background: var(--white);
    height: 100%;
    width: 81.75%;
    padding: 0;
  }

  &.visible {
    transform: translate(0);
    left: 0;

    & .reportViewer {
      -webkit-box-shadow: 8px 0px 6px -6px #777;
      -moz-box-shadow: 8px 0px 6px -6px #777;
      box-shadow: 8px 0px 6px -6px #777;
    }
  }
}

#displayReportConflictosRight_container {
  position: absolute;
  display: flex;
  z-index: 11;
  right: 0;
  top: 0;
  height: 100%;
  width: 45%;
  transform: translate(100%);
  transition: all 0.2s;
  background-color: rgba(54, 54, 53, 0.445);

  &.visible {
    transform: translate(0);
    right: 0;

    & .reportViewer {
      -webkit-box-shadow: 8px 0px 6px -6px #777;
      -moz-box-shadow: 8px 0px 6px -6px #777;
      box-shadow: 8px 0px 6px -6px #777;
    }
  }
}