.position-absolute {
    position: absolute;

    &-0 {
        @extend .position-absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    &-top {
        @extend .position-absolute;
        top: 0;
    }

    &-bottom {
        @extend .position-absolute;
        bottom: 0;
    }

    &-left {
        @extend .position-absolute;
        left: 0;
    }

    &-right {
        @extend .position-absolute;
        right: 0;
    }

    &-top-left {
        @extend .position-absolute;
        top: 0;
        left: 0;
    }

    &-top-right {
        @extend .position-absolute;
        top: 0;
        right: 0;
    }

    &-bottom-left {
        @extend .position-absolute;
        bottom: 0;
        left: 0;
    }

    &-bottom-right {
        @extend .position-absolute;
        bottom: 0;
        right: 0;
    }

    &-center {
        @extend .position-absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &-center-top {
        @extend .position-absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
    }

    &-center-bottom {
        @extend .position-absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
    }

    &-center-left {
        @extend .position-absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
    }

    &-center-right {
        @extend .position-absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
    }
}