@media (min-width: 768px) {
    #dxForm_dxDataGrid_NuevoPedido .dx-field-item
    {
        padding: 5px 5px 5px 0px;
    }
}

.contenedorSeparadoGrids{
    border-top: solid 1px #ddd;
    align-items: center;
}

.tituloSeparadorGrids{
    color:var(--primary);
    font-weight: 500;
    line-height: 20px;
    white-space: nowrap;
}

.subTituloSeparadorGrids{
    color:#BBB;
    font-weight: 500;
    line-height: 20px;
}

#dxScrollView_nuevoPedido{
    .dx-scrollable-scrollbar.dx-widget.dx-scrollbar-vertical.dx-scrollbar-hoverable{
        z-index:6;
    }
}

#dxDataGrid_NuevoPedido, #dxDataGrid_NuevoPedido_almacen
{
    .dx-freespace-row {  
        height: 0px !important;  
    } 
}
 
#dxDatagrid_pedidosAnteriores.dx-state-disabled{
    opacity:1;
}

#dxDatagrid_pedidosAnteriores
{
    .dx-master-detail-cell
    {
        padding:2px 2px 2px 12px;

    }
}

#Pedidos .dx-accordion-item-body
{
    padding-bottom:1px;
}

#Pedidos .dx-accordion-item-title{
    padding: 6px 5px 12px 5px !important;
    font-size:1.2rem !important;
}

#dxDateBox_dbFechaPedido .dx-texteditor-input
{
    padding-bottom:6px;
}

.dx-field-value.dx-datebox {  
    min-width: 120 !important;  
} 

#dxButton_atras_selEntidad
{
    .dx-icon{
        font-size:14px;
        margin-right: 0px;  
        padding: 0px;  
    }
    
    .dx-button-content 
    {
        padding:0px !important;
    }
}


@media (max-width: 767.98px) {
    #dxDataGrid_NuevoPedido
    {
        .dx-dropdownmenu-button.dx-button-mode-contained
        {
            margin-left:3px;
        }
    
        .dx-command-expand.dx-datagrid-group-space
        {
           width:1px !important;
           min-width: 1px;
        }
    
        .dx-command-edit.dx-command-edit-with-icons
        {
            width:25px !important;
            min-width: 25px;
        }

        td{
            padding:5px;
        }

        .dx-datagrid-rowsview .dx-adaptive-detail-row .dx-adaptive-item-text
        {
            padding:0px;
        }
        .container_spanCentrado
        {
            padding-left: 0px !important;
        }

        input{
            min-height:0px;
            padding:0px;
        }

        .dx-layout-manager .dx-field-item:not(.dx-last-col)
        {
            padding-right:0px;
        }
    }
  
    .dx-accordion-item
    {
        border-top: 1px solid #ddd !important;
        border-bottom: 1px solid #ddd !important;
    }

    #dxScrollView_pedidos .dx-scrollable-content
    {
        padding-top:0px !important;
        padding-bottom:0px !important;
    }
}

.dx-scrollable-native.dx-scrollable-native-ios .dx-scrollable-content
{
    min-height:100% !important;
}

#dxForm_dxDataGrid_NuevoPedido .dx-field-item.dx-last-col
{
    padding-right:0px;
    padding-bottom: 10px;
}

#dxScrollView_pedidos .dx-scrollview-content{
    height:100%;
}

#dxScrollView_pedidos .dx-scrollable-content{
    padding-right:0px !important;
}