#CalendarioLaboral_RRHH {
  & .month-container {
    // height: 210px !important;
    width: 20% !important;
  }

  .dx-toolbar-before {
    padding-right: 0px;
    width: 100%;
  }

  .separadorCalendarioEstado {
    flex-basis: 10px;
  }

  /* Para das cerrados, tacha el da con X */
  & .diaCierre {
    position: relative;
  }

  & .diaCierre::before,
  .diaCierre::after {
    content: "";
    width: 65%;
    position: absolute;
    right: 6px;
    top: 46%;
  }

  & .diaCierre::before {
    border-bottom: 2px solid #ab2a3e;
    -webkit-transform: skewY(-22.5deg);
    transform: skewY(-22.5deg);
  }

  & .diaCierre::after {
    border-bottom: 2px solid #ab2a3e;
    -webkit-transform: skewY(22.5deg);
    transform: skewY(22.5deg);
  }
}