#Selector {
    .dx-scrollable-content, .dx-scrollview-content.dx-tileview-wrapper {
      height: 100% !important;
    }

    .cardContainer .cardHeader span {
      font-size: 20px;
    }
  
    .cardContainer .dx-tile {
      border-radius: 3px;
      border: 1px solid rgba(221, 221, 221, 0.6) !important;
    }
  
    .cardContainer .dx-tile.dx-state-hover {
      background-color: #edb637;
      border: 1px solid transparent;
    }
  
    .cardContainer .dx-tile.dx-state-active {
      background-color: #f5dca3;
      border: 1px solid transparent;
    }
  
    .cardContainer .dx-button span {
      font-size: inherit;
      color: rgb(120, 120, 120);
    }
  
    .cardContainer .dx-button i {
        font-size: 25px;
        height: auto;
        width: 25px;
    }
  
    .cardContainer .dx-button.dx-state-active svg {
      fill: #edb637 !important;
    }
  
    .cardShadow {
      -webkit-box-shadow: 3px 5px 6px -6px #777;
      -moz-box-shadow: 3px 5px 6px -6px #777;
      box-shadow: 3px 5px 6px -6px #777;
    }
  
    .container_iconos {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap;
      flex-flow: row wrap;
    }
  
    .container_iconos span {
      width: 35px;
      font-size: 20px;
      font-weight: lighter;
      text-align: right;
      padding: 0px 5px;
    }
  
    .container_iconos svg {
      fill: rgb(120, 120, 120);
      height: 25px;
      width: 35px;
      max-height: 25px;
      max-width: 35px;
    }
  
    .container_iconos div div:hover svg {
      fill: #edb637;
    }
  
    .container_iconos div div:hover span {
      color: #edb637;
    }

    #dxBox_tileViews{
        position: relative;
    }

    .dxTileView_Box{
        padding:2px;
    }

    #dxTileView_Box > .dx-item:nth-child(2) > .dx-box-item-content
    {
        height: 100%;
        background-color: white;
        position: relative;
        overflow: hidden;
        font-size: 16px;
        font-weight: lighter;
        color: #7d7d7d;
    }
  
    .cardContent .dx-datagrid {
      color: #7d7d7d;
    }
  
    .cardHeader {
      justify-content: center;
      padding-left: 10px;
      border-bottom: 1px solid gainsboro;
      font-weight: lighter;
    }

    .border-tile{
      border: 1px solid rgba(221, 221, 221,1)
    }
  
    .cardContainer .dx-state-hover .cardHeader span {
      color: white;
    }
  
    .cardContainer .dx-state-active .cardHeader span {
      color: #515151;
    }
  
    .cardContent .dx-datagrid-content .dx-datagrid-table .dx-row > td,
    .cardContent .dx-datagrid-content .dx-datagrid-table .dx-row > tr > td {
      vertical-align: inherit;
    }
  
    .dxCard_front,
    .dxCard_back {
      height: 100%;
      width: 100%;
    }
  
    .dxCard_front {
      position: absolute;
      display: block;
      padding: 5px;
    }
  
    .dxCard_back {
      position: relative;
      display: none;
      padding: 20px;
      padding-bottom:5px;
    }
  
    .activeCard {
      height: 100%;
      width: 100%;
      padding-top: 15px;
    }
  
    .activeCard .dxCard_front {
      -webkit-animation: slide-out-left 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
      animation: slide-out-left 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    }
  
    .activeCard .dxCard_back {
      display: block;
      -webkit-animation: slide-in-right 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
      animation: slide-in-right 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    }
  
    .onlyActive {
      display: none;
    }
  
    .activeCard .onlyActive {
      display: block !important;
      margin-right: 25px;
    }
  
    @-webkit-keyframes slide-out-left {
      0% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
      }
  
      100% {
        -webkit-transform: translateX(-1000px);
        transform: translateX(-1000px);
        opacity: 0;
      }
    }
  
    @keyframes slide-out-left {
      0% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
      }
  
      100% {
        -webkit-transform: translateX(-1000px);
        transform: translateX(-1000px);
        opacity: 0;
      }
    }
  
    @-webkit-keyframes slide-in-right {
      0% {
        -webkit-transform: translateX(1000px);
        transform: translateX(1000px);
        opacity: 0;
      }
  
      100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
      }
    }
  
    @keyframes slide-in-right {
      0% {
        -webkit-transform: translateX(1000px);
        transform: translateX(1000px);
        opacity: 0;
      }
  
      100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
  