.llamamientoCardList {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem;

  .lavanderiaCard {
    .categoriaInternaList {
      display: flex;
      flex-direction: column;

      div {
        padding: 0.5rem 0;
      }

      div:not(:last-child) {
        border-bottom: 1px solid var(--lightGray);
      }
    }
  }

  .cardSelected {
    cursor: pointer;
    filter: brightness(95%);
    -webkit-filter: brightness(95%);
  }

  .lavanderiaCard:hover {
    cursor: pointer;
    filter: brightness(90%);
    -webkit-filter: brightness(90%);
  }

  .lavanderiaCard:active {
    filter: brightness(85%);
    -webkit-filter: brightness(85%);
  }
}

.llamamientoGrid {
  // .dx-data-row:not(.dx-selection):active {
  //     filter: brightness(75%);
  //     -webkit-filter: brightness(75%);
  // }

  .botonAsociarPersonaGeneral {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    .dx-button-content {
      padding: 0;
    }

    span {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .asociar-disabled {
    pointer-events: none;
  }
}

.popup-content,
.card-content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.card-content {
  padding: 1.25rem;
}
