#dxPopup_cierrePrecios{
    .dx-calendar-cell{
        padding:0px;
    }
    .dx-calendar-cell:not(.dx-calendar-selected-date){
        padding:0px;
        div.fechaCerrada{
            color:rgba(94,94, 94, 0.5);
            border-radius: 4px;
            font-weight: 400;
            font-size: 15px;
        }
    }

    .icon_lockOutline, .icon_Warning{
        position: absolute;
        bottom:9px;
        pointer-events:none;
        color:rgba(94,94, 94, 0.5) !important;
    }

    .dx-calendar-selected-date{
        .icon_lockOutline{
            color:white !important;
        }
    }

    .disabledDay{
        pointer-events: none;
    }
}