#dxButtonGroup_formularioVisibles, #dxButtonGroup_formulariosInicio
{
  .dx-checkbox-icon:before{
      color:#FFF;
  }
}

#dxPopup_dxTagBox_idCategoriaInterna{
  .dx-list-group-header{
    padding-top: 10px;
  }
}

#dxButtonGroup_formInicio_scrollview, #dxScrollView_dxButtonGroup_formulariosNUsuario {
  height:auto;
}

#dxButtonGroup_formularioVisibles{
  padding-bottom:0px !important;
  .dx-item.dx-buttongroup-item {
    min-width: 190px;
  }
}

#dxButtonGroup_formulariosInicio{
  .dx-item.dx-buttongroup-item {
    min-width: 110px;
  }
}

#dxForm_ResumenDatosUser
{
    .dx-layout-manager.dx-widget,
    .dx-form-group-content,
    .dx-field-item,
    .formGroup_resumenLavanderias .dx-form-group,
    .formGroup_resumenLavanderias .dx-field-item-content,
    .dx-item.dx-box-item
    {
      height:100% !important;
    }
}

#dxList_resumenLavanderias .dx-scrollable-content, #dxList_resumenLavanderias .dx-scrollview-content{
  height: 100%;
}

.cursorDefault .dx-item-content.dx-list-item-content{
  cursor:default !important;
}

#dxBox_resumenCentrosTrabajo > div:nth-child(3n) {
    border-bottom: 1px solid #ddd;
}

#dxList_resumenLavanderiasSel{
  .dx-list-static-delete-button{
    border:none !important;
  }
}

#dxBox_resumenLavanderiasSel{
  padding:10px 15px 10px 15px;
  border-radius: 15px;
  background-color: var(--light);
  box-shadow: 0px 0px 5px 1px #ccc;
  margin:0px 5px 5px 0px;
}