#InfComparativaProcesadosEntregados
{
  #dxToolbar_title .dx-toolbar-before
  {
    width:calc(100% - 340px)
  }
    .dxChart_contenedorIndividual {
      width:150px;
      padding: 3px 10px;
      border-radius: 3px;
      border: 1px dotted grey;
      white-space: nowrap;
      text-align: left;
      color: grey;
      //margin-right: 45px;
  }

  #dxToolbar_title .dx-toolbar-after .dx-toolbar-button:nth-last-child(2) .dxChart_contenedorIndividual {
    margin-right: 40px;
  }

  .lblKgLavados, .kgLavadosProcesados{
    text-align: left;
    color: rgba(128, 128, 128, 1);
    font-size: 18px;
    font-weight: 600;
  }
}
