#CalendarioLaboral {
  & .circulo {
    height: 10px;
    width: 10px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
  }
  
  .svg_warning{
    fill:rgb(255, 35, 0) !important;
  }

  .backgroundWhite{
    background-color: #FFFFFF !important;
  }

  & #dxToolbar_personas .dx-toolbar-before {
    width: 100%;
    padding-right: 0px;
  }

  /*  Estilos calendario year-calendar    */
  & .calendarLaboral,
  .calendarLavanderia {
    color: #484848;
    font-size: 12px;
    font-family: "Helvetica Neue", "Segoe UI", "Helvetica", "Verdana", "sans-serif";
    padding-top: 0px;
    overflow: auto;
  }

  & .year-title {
    font-weight: 600 !important;
  }

  & .calendarLaboral .day > div {
    font-size: 12px;
    font-family: "Roboto Medium", "Helvetica", "Droid Sans", "Tahoma", "Geneva", "sans-serif";
    width: 35px;
    height: 24px;
  }

  & .calendarLavanderia .day > div {
    font-size: 13px;
    font-family: "Roboto Medium", "Helvetica", "Droid Sans", "Tahoma", "Geneva", "sans-serif";
    width: 52px;
    height: 24px;
  }

  & .month {
    border-collapse: separate;
  }

  & .calendarLaboral thead:after,
  .calendarLavanderia thead:after {
    line-height: 1px;
    content: "_";
    color: transparent;
    display: block;
  }

  & .month-title {
    color: #999999;
    font-family: "Roboto Medium", "Helvetica", "Droid Sans", "Tahoma", "Geneva", "sans-serif";
    font-size: 16.1px;
    padding: 2px 0px !important;
    border: 1px #d9d9d9 solid !important;
    border-radius: 4px !important;
  }

  & .month tr:nth-child(2n) th {
    padding: 5px 5px 2px 5px !important;
    text-transform: uppercase;
    color: #999999;
    font-size: 12px;
    border-bottom: 1px solid #999999;
  }

  & .calendar-header {
    border-radius: 4px;
  }

  & .calendarLaboral .months-container .month-container,
  & .calendarLavanderia .months-container .month-container {
    height: 210px !important;
  }

  & .months-container {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
    height:auto;
  }

  & .calendarLaboral table.month td.day .day-content,
  & .calendarLaboral table.month .range-start,
  & .calendarLaboral table.month .range-end {
    border-radius: 0px !important;
  }

  & .calendarLavanderia table.month td.day .day-content,
  & .calendarLavanderia table.month .range-start,
  & .calendarLavanderia table.month .range-end {
    border-radius: 0px !important;
  }

  /* FlexBox leyendas*/
  & .containerPadre_flex {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    flex-flow: row wrap;
  }

  /* Para das cerrados, tacha el da con X */
  & .diaCierre {
    position: relative;
    display: inline-block;
  }

  & .diaCierre::before,
  .diaCierre::after {
    content: "";
    width: 65%;
    position: absolute;
    right: 9px;
    top: 46%;
  }

  & .diaCierre::before {
    border-bottom: 2px solid #ab2a3e;
    -webkit-transform: skewY(-22.5deg);
    transform: skewY(-22.5deg);
  }

  & .diaCierre::after {
    border-bottom: 2px solid #ab2a3e;
    -webkit-transform: skewY(22.5deg);
    transform: skewY(22.5deg);
  }
}
