@font-face {
  font-family: "inf_icons";
  src: url("./fonts/icons.eot?5kiojs");
  src: url("./fonts/icons.eot?5kiojs#iefix") format("embedded-opentype"),
    url("./fonts/icons.ttf?5kiojs") format("truetype"), url("./fonts/icons.woff?5kiojs") format("woff"),
    url("./fonts/icons.svg?5kiojs#inf_icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="inf_icons_"],
[class*=" inf_icons_"] {
  font-family: "inf_icons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: var(--secondary);
}

.has-active-child [class^="inf_icons_"],
.has-active-child [class*=" inf_icons_"],
.active [class^="inf_icons_"],
.active [class*=" inf_icons_"] {
  color: var(--white);
}

