#RegistroActividad-Container {
    background-color: var(--light);
    height: 100%;
    width: 100%;
    
    border: 1px solid #ddd;

    border-radius: 4px;
    
    .RegistroActividad-FeedContainer{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column-reverse;
        padding: 10px 15px;
    }
    
    .itemActividad {
        display: flex;
        flex-direction: column;

        &:not(:first-child) {
            padding-bottom: 15px;
        }

        .itemActividad-Value {
            display: flex;
            flex-direction: row;

            >span{
                flex: 1;
                display: flex;
                flex-direction: column;
                background-color: white;
                border-radius: 0 10px 10px 10px;
                box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
                padding: 5px 7.5px;
                transition: 0.1s ease-in-out;
                position: relative;

                &:hover {
                    background-color: #fafafa;
                }
            }
    
            min-height: 30px;

            >span{
                word-break: break-word;
            }
        }

        .itemActividad-Info {
            margin-bottom: 2.5px;
            gap: 5px;
            color: var(--secondary);
        }

        .itemActividad-Autor {
            font-size: 0.75rem;
            font-weight: 500;
        }

        .itemActividad-TimeStamp {
            display: inline-flex;
            align-items: center;
        }
    }
}