// Header Dark
.app-header {
    &.header-text-dark {
        .app-header-right {
            .icon-wrapper-alt {
                i {
                    font-size:1.7rem;
                }
            }
        }
        .icon-wrapper-alt:hover {
            i {
              background-color: white;
            }
          }
    }
}

.gridAplicaciones{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 10px;
  padding: 0px 15px;
  margin: 0 auto;
  width: 100%;
}

.bg-dark-gray
{
  background-color: #817f80;
}

.ps-show-always.ps.ps--active-y > .ps__rail-y {
    opacity: 0.6;
  }
  
  .second-sidebar-visible .secondarySideBar{
   transform:translate3d(280px, 0, 0) ;
   transition:transform ease 250ms;
  }
  
  .btnAplicacion span
  {
    width:100%;
    height:100%;
    margin-top: 3px;

    display: flex !important;
    justify-content: center;
    align-items: center;
  }

  .btnApp.btnAplicacion span
  {
    color:var(--appColor) !important;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box !important;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  
  .btnAplicacion:hover, .btnAplicacion.activo
  {
    span
    {
      word-break: break-word;
      white-space: normal;
      color:white !important;

      display: block !important;
      -webkit-box-orient: unset;
      -webkit-line-clamp: unset;
    }

    i{
      background-image:unset !important;
      background-color:white !important;
    }  
  }

  .btnAplicacion.btnApp:hover, .btnAplicacion.btnApp.activo {
    background-color:var(--appColor) !important;
  }
  
  .btnAplicacion i{
    font-size: 3.2rem !important;
    width: 100%;
    background-color:var(--appColor) !important;
  }
  
  .btnAplicaciones_container{
    height:100% !important;

    .btnApp{
      text-align: center;
    }

    button{
      height: 100% !important;
      min-height: 113px !important;
      display: flex;
      flex-direction:column;
    }
  
    @media (max-width: 991.98px) {
      height:93px;
      
      button{
        min-height: 65px !important;
      }
      
      i {
        display: block !important;
      }
  
      span {
        white-space: normal;
      }
    }
  }
  
  .header-mobile-open
  {
    border-radius: 12px !important;
    flex-flow: wrap;
    overflow: visible !important;
  
    i{
      font-size: 2rem !important;
    }
  
    .language-icon{
      font-size: 1.65rem !important;
    }

    .header-dots{
      margin:0 auto;

      .dropdown.show{
        position:static !important;
      }
  
      .dropdown-menu {
        position: absolute !important;
        top:100% !important;
        transform: translateY(10%) !important;  
      }
    }

    .header-btn-lg .rm-pointers.dropdown-menu-xs.dropdown-menu-right{
      position: fixed !important;
      top: unset !important;
      transform: translateY(60%) !important;  
    }
  }

  .filter-blur-5{
      filter:blur(5px);
  }
