.dx-datagrid-filter-row .dx-slider {
    padding-left: 30px;
    padding-right: 40px;

    & .dx-slider-handle {
        margin-top: -7px;
        height: 14px;
        border-radius: 10px;
    }

    & .slider-textValue {
        font-size: 0.9rem;
        position: absolute;
        top: 5px;
        right: 0px;
    }
}
