.cellColumn {
  vertical-align: middle !important;
  position: relative;
}

.cellColumn div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  text-align: center;
  color: #646464;
  font-weight: 500;
  border-radius: 200px;
}

.cellColumn div span {
  pointer-events: none;
}

.cellColumn div span:last-child {
  font-size: 16px;
  font-weight: 700;
}

.cssPrevGrid-br {
  border-radius: 0 !important;
}

.cssPrevGrid-br:first-child {
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
}

.cssPrevGrid-br:last-child {
  border-bottom-left-radius: 10px !important;
  border-bottom-right-radius: 10px !important;
}

.cellColumn:hover div {
  cursor: pointer;
  color: #edb637;
}

.cellColumn:active {
  filter: brightness(80%);
  -webkit-filter: brightness(80%);
}

.cellPerson span,
.cellPersonCompacto span {
  overflow: hidden;
  text-overflow: ellipsis;
  color: #646464;
  font-weight: 400;
  word-break: normal;
  height: 100%;
}

.cellPersonCompacto {
  & span {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    word-break: break-all;
  }

  & .fotoPersona {
    border-radius: 500px;
    min-width: 30px;
    width: 30px;
    min-height: 30px;
    height: 30px;
  }
}

.cellData {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 8px !important;
  margin: auto;
  box-shadow:
    rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 4px 2px;
  word-wrap: break-word;
  overflow: hidden;

  & i {
    font-size: 1.8rem;
    pointer-events: none;
  }
}

.cellDataCompacto {
  width: 85%;
  height: 80%;
}

.cellData span:first-child {
  font-weight: 700;
}

.cellData:hover {
  cursor: pointer;
  outline: solid 3px #edb637;
  outline-offset: 0px;
  transition: none !important;
}

.cellData:active {
  filter: brightness(75%);
  -webkit-filter: brightness(75%);
}

.cellDataDetailed {
  width: 9rem;
  height: 4rem;
  margin: 0;

  & span {
    width: 100%;
    text-align: center;
    overflow: hidden;
    white-space: normal;
  }
}

.stripe {
  // color: white;
  background: repeating-linear-gradient(
    -45deg,
    #ffffff,
    #ffffff 5px,
    #ffffff00 5px,
    #ffffff00 10px
  );
  border-color: #fff !important;
}

.wavy {
  border-color: #fff !important;
  background:
    linear-gradient(135deg, rgba(255, 255, 255, 1) 25%, transparent 25%) -16px 0,
    linear-gradient(225deg, rgba(255, 255, 255, 1) 25%, transparent 25%) -16px 0,
    linear-gradient(315deg, rgba(255, 255, 255, 1) 25%, transparent 25%),
    linear-gradient(45deg, rgba(255, 255, 255, 1) 25%, transparent 25%);
  background-size: 10px 10px;
}

.bloqueado:hover {
  cursor: not-allowed !important;
}

.grandTotalCount > div:not(:last-child),
.grandTotalCount > div:first-child {
  filter: brightness(0.8) saturate(3);
}

.grandTotalCount:hover {
  cursor: pointer;
}

.bordeSemana:not(.bordeCierreNomina) {
  border-left: none !important;
}

.bordeSemana:not(:last-child, .bordeCierreNomina) {
  border-right: 2px #ddd solid !important;
}

.bordeCierreNomina {
  border-right: 2px var(--danger) solid !important;
  border-left: none !important;
}

.iconoAlertaDia {
  display: flex;
  flex-direction: column;
  gap: 2px;

  position: absolute;
  top: 0;
  right: calc(-1.5rem / 3);
  z-index: 1;

  font-size: 1.25rem;

  i {
    background-color: var(--danger);
    color: white !important;
    padding: 2px;
    border-radius: 7.5px;

    &:hover {
      color: white !important;
    }
  }
}

.diaCierre {
  background-color: #f5f5f5 !important;
}

.diaCierre::before,
.diaCierre::after {
  content: "";
  width: 100%;
  position: absolute;
}

.diaCierre::before {
  border-bottom: 4px solid var(--danger);
  -webkit-transform: skewY(-22.5deg);
  transform: skewY(-22.5deg);
  z-index: 0;
}

.diaCierre::after {
  border-bottom: 4px solid var(--danger);
  -webkit-transform: skewY(22.5deg);
  transform: skewY(22.5deg);
  z-index: 0;
}

.diaCierre span {
  z-index: 1;
  text-shadow:
    1px 1px 2px rgba(245, 245, 245, 1),
    -1px 1px 2px rgba(245, 245, 245, 1),
    -1px -1px 2px rgba(245, 245, 245, 1),
    1px -1px 2px rgba(245, 245, 245, 1);
}

.rotate {
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.overflow-visible {
  overflow: visible !important;
}

.personRow {
  .personOption {
    cursor: pointer;
    opacity: 0;
  }

  &:hover {
    .personOption {
      opacity: 1;
    }
  }
}

.personLabel {
  span:not(:first-child) {
    color: var(--secondary);
    font-size: 0.8rem;
  }
}

.personOption {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  gap: 3px;

  & i {
    background-color: white;
    padding: 3px;
    border-radius: 5px;
    font-size: 1.35rem;
  }
}

.fijarSemana .dx-button-content {
  padding: 5px 10px;
}

.CuadrantePersonalDataGrid {
  [role="columnheader"] {
    overflow: visible;
  }

  [role="gridcell"] {
    height: 1px;
  }

  .dx-group-row:hover {
    cursor: pointer;
  }
}
