#reportViewer_container {
  position: absolute;
  display: flex;
  z-index: 11;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transform: translate(-100%);
  transition: all 0.2s;
  background-color: rgba(54, 54, 53, 0.445);

  #reportViewer_close {
    flex: 1;

    & :before{
    color: rgb(54, 54, 53) !important;
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.5);
      & :before{
        color: white !important;
        }
    }
  }
  .reportViewer {
    background: var(--white);
    flex: 0 0 80%;
    height: 100%;
    padding: 0;
  }

  &.opened {
    transform: translate(0);
    left: 0;

    & .reportViewer {
      -webkit-box-shadow: 8px 0px 6px -6px #777;
      -moz-box-shadow: 8px 0px 6px -6px #777;
      box-shadow: 8px 0px 6px -6px #777;
    }
  }
}

@media all and (max-width: 900px) {
  #reportViewer_container {
    flex-direction: column;
  }
}
