#DashboardFinanciero {
  .row-1 {
    height: 38vh;
    min-height: 360px;
  }


  #LinearGauge {
    width: 100%;
    height: 100%;
    background-color: rgb(249, 249, 249);
    border-radius: 5px;
    box-shadow: 0 0.125rem 0.625rem rgba(48, 48, 48, 0.17), 0 0.0625rem 0.125rem rgba(48, 48, 48, 0.17);
  }

  #CircularGauge {
    width: 100%;
    height: 100%;
   min-height: 75px;
    min-width: 150px;
    padding: 0px 5px;
  }

  #Chart {
    width: 100%;
    height: 100%;
  }

  .dx-tooltip-wrapper .dx-popup-content > div{
    width: 100%;
    max-width: 500px;
    font-size: 1.2rem;
    white-space: pre-wrap;
    word-break: break-word;
    display: table-cell;
  }
}
