svg:not(.dxc-chart):not(.dxrs-range-selector) {
  fill: var(--secondary);

  &:hover {
    fill: var(--primary);
  }
}

.dx-icon,
.dx-dropdowneditor-icon,
[class^="icon_"],
[class*=" icon_"] {
  color: var(--secondary) !important;

  &:hover {
    color: var(--primary) !important;
  }

  &.primary::before {
    color: var(--primary) !important;
  }
  &.success::before {
    color: var(--success) !important;
  }
  &.danger::before {
    color: var(--danger) !important;
  }
  &.warning::before {
    color: var(--warning) !important;
  }
  &.light::before {
    color: var(--light) !important;
  }
  &.lightGray::before {
    color: var(--lightGray) !important;
  }
  &.smokedWhite::before {
    color: var(--smokedWhite) !important;
  }
}

//BUTTONS
.dx-button-danger,
.dx-button-default,
.dx-button-success,
.dx-revert-button {
  & .dx-icon {
    color: white !important;

    &:hover {
      color: white !important;
    }
  }
}

//EDITORS
.dx-button:not(.dx-button-danger):not(.dx-button-default):not(.dx-button-success):not(
    .dx-revert-button
  ):hover,
.dx-button-normal:not(.dx-revert-button):hover,
.dx-clear-button-area:hover,
.dx-state-hover.dx-datagrid-search-panel,
.dx-state-hover.dx-menu-item-has-icon,
.dx-state-hover.dx-lookup {
  & .dx-icon,
  & .dx-dropdowneditor-icon,
  & .dx-icon:before,
  &.dx-icon:before,
  & .dx-lookup-arrow {
    color: var(--primary) !important;
  }

  & svg {
    fill: var(--primary) !important;
  }
}

.dx-datagrid .dx-header-filter-empty:hover,
.dx-list .dx-list-reorder-handle:hover {
  color: var(--primary) !important;
}

//GRID
.dx-datagrid {
  & .dx-datagrid-rowsview .dx-selection.dx-row td:not(.dx-editor-cell, .not-white),
  & .dx-datagrid-rowsview .dx-selection.dx-row td.dx-command-select {
    & svg {
      fill: white !important;
    }
    & .dx-icon,
    & :before,
    & [class^="icon_"] {
      color: white !important;
    }

    & .dx-checkbox-icon {
      background-color: white;
      border: 2px solid white !important;
      &:before {
        color: var(--primary) !important;
      }
    }
  }
}

//LIST
.dx-list {
  & .dx-list-item {
    &.dx-list-item-selected {
      background-color: var(--primary) !important;

      & div,
      & [class^="icon_"],
      & .dx-icon {
        color: white !important;
      }
      & .dxToolbar_opcion > div {
        color: var(--secondary) !important;
      }
      & .dxToolbar_opcion:hover > div {
        color: white !important;
      }

      & svg {
        fill: white !important;
      }

      & .dx-checkbox-icon {
        background-color: white;
        border: 2px solid white !important;
      }

      & .dx-list-static-delete-button {
        border: 2px solid white;
        & .dx-icon {
          color: white !important;
        }
      }
    }
  }
}

//RADIO GROUP
.dx-radiobutton.dx-item-selected {
  :before {
    color: white;
  }
}

@font-face {
  font-family: "icon";
  src: url("./fonts/icons.eot?5kiojs");
  src: url("./fonts/icons.eot?5kiojs#iefix") format("embedded-opentype"),
    url("./fonts/icons.ttf?5kiojs") format("truetype"),
    url("./fonts/icons.woff?5kiojs") format("woff"),
    url("./fonts/icons.svg?5kiojs#icon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon_"],
[class*=" icon_"] {
  font-family: "icon";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: var(--secondary);

  &.primary::before {
    color: var(--primary) !important;
  }
  &.info::before {
    color: var(--info) !important;
  }
  &.turquoise::before {
    color: var(--turquoise) !important;
  }
  &.success::before {
    color: var(--success) !important;
  }
  &.danger::before {
    color: var(--danger) !important;
  }
  &.warning::before {
    color: var(--warning) !important;
  }
  &.light::before {
    color: var(--light) !important;
  }
  &.white::before {
    color: white !important;
  }
  &.smokedWhite::before {
    color: var(--smokedWhite) !important;
  }
}

.has-active-child [class^="icon_"],
.has-active-child [class*=" icon_"],
.active [class^="icon_"],
.active [class*=" icon_"] {
  color: var(--white);
}

