
  .degradadoLista {
    position: relative;
  }

  .degradadoLista::after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: -5px;
    left: 0;
    pointer-events: none;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 90%);
    width: 100%;
    height: 20px;
  }

.dx-list{
  .custom-disabled .dx-list-item,
  .custom-disabled.dx-list-item {
    background-color: transparent;
    opacity: .6;
  }
}