.dx-tabpanel {
  display: flex;
  flex-direction: column;

  & .noPadding .dx-item-content.dx-multiview-item-content
  {
    padding: 0px;
  }
}

@-moz-document url-prefix() {
  .dx-multiview-item.dx-item-selected {
    position: absolute;
  }
}

.dx-tabpanel .dx-multiview-wrapper {
  border: none !important;
  background-color: transparent !important;
  padding-top: 15px;
}

.dx-tabpanel-container {
  flex: 1;
}

.dx-tabpanel .dx-tabs {
  font-size: inherit;
}

.dx-tabpanel.no-header {
  & .dx-tabpanel-tabs {
    display: none;
  }

  & .dx-multiview-wrapper {
    padding-top: 0px;
  }
}

.dx-tabpanel.visible-header {
  & .dx-tabpanel-tabs {
    display: block;
  }

  & .dx-multiview-wrapper {
    padding-top: 15px !important;
  }
}

//REGION  SI TIENE UN SCROLLVIEW SUPERIOR MANTIENE EL HEADER AL HACER SCROLL
// HAY QUE AADIR EVENTO dxTabHeader_fijo_onScroll
.dxTabHeader_fijo .dx-tabpanel-container .dx-multiview-wrapper,
.dxTabHeader_fijo .dx-item-content.dx-multiview-item-content
{
    padding-top:40px;
}

.dxTabHeader_fijo .dx-tabpanel-tabs{
    position:absolute;
    z-index:12000;
    background-color: #FFFFFF;
    padding-bottom:25px;
}
//ENDREGION

.overflow_visible > .dx-tabpanel-container {
  & > .dx-multiview-wrapper {
      overflow: visible !important;

      & > .dx-multiview-item-container > .dx-multiview-item{
        overflow: visible !important;

        &:not(.dx-item-selected):not(.dx-multiview-item-hidden){
          top:20px;
                }
      }


      // & > .dx-multiview-item-container > .dx-multiview-item
  }
}