.resumenCard {
    outline: 2px solid var(--cardColor);
    color: var(--cardColor);
    
    i {
        color: var(--cardColor) !important;
        pointer-events: none;
    }

    .resumenCard-title {
        font-size: 15px;
        font-weight: 600;
        color: #999;
    }

    .resumenCard-body {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        font-size: 25px;
        font-weight: 700;
        color: #646464;
    }
}

.month-selector {
    height: 1px;
    .dx-highlight-outline{
        padding: 0;
        height: 100%;
    }
}

.month-selected {
    background-color: rgb(135, 238, 150);
    color: white !important;
}

.dx-switch-header {
    .dx-switch-container{
        border-top: none;
        border-left: none;
        border-right: none;
        border-radius: 0;

        .dx-switch-on,.dx-switch-off {
            color: inherit;
        }

        .dx-switch-handle:before {
            background-color: #edb637;
        }
    }
}

#dxDataGrid_ControlPresupuestario {

    .columnOverflow {
        overflow: visible !important;
    }

    .editorAjuste {
        outline: 2px solid lightgray;
        outline-offset: -1px;

        &:hover {
            background-color: white;
            filter: brightness(0.95);
        }
    }

    .dx-row {

        .icono-comentario {
            display: none;
        }

        &.dx-state-hover{
            .icono-comentario {
                display: block;
            }
        }
    }
}