@media (min-width: 0px) { // xs - Mvil
  html {
    font-size: 8px;
  }
}

@media (min-width: 768px) { // sm - Tablet
  html {
    font-size: 10.5px;
  }
}

@media (min-width: 992px) { //md - iPad Pro
  html {
    font-size: 11px; 
  }
}

@media (min-width: 1200px) { //lg - Portatil MDPI
  html {
    font-size: 12px; 
  }
}

@media (min-width: 1400px) { //Portatil HiDPI
  html {
    font-size: 13px; 
  }
}

@media (min-width: 1600px) { //lg - PC Sobremesa
  html {
    font-size: 14px; 
  }
}

