.formContainer {
  height: 100%;
  padding-top: 20px;
}

.titForm {
  width: 100%;
  min-width: 25%;
  max-width: 400px;
  padding-left: 10px;
  border-bottom: 2px solid #edb637;
  margin-bottom: 20px;
}

.titForm h3 {
  font-family: "Segoe UI Light", "Helvetica Neue Light", "Segoe UI", "Helvetica Neue", "Trebuchet MS", Verdana;
  font-size: 25px;
  font-weight: bold;
  text-align: left;
  margin-top: 5px;
}

