.logo-Estrella {
  background: url("utils/images/Estrella.svg");
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
}

.logo-EstrellaPolarier {
  background: url("utils/images/EstrellaPolarier.svg");
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
}

.logo-LoveYourLinen {
  background: url("utils/images/LoveYourLinen.svg");
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
}

.logo-MyPolarier {
  background: url("utils/images/MyPolarier.svg");
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
}

.img-CabeceraPolarier {
  background: url("utils/images/CabeceraPolarier.png");
  background-size: 120% 100% !important;
  opacity: 1 !important;
  filter: grayscale(30%) !important;
}

.underline_bottom_dotted {
  border-bottom: 2px dotted lightgray;
}

.underline_bottom_dashed {
  border-bottom: 2px dashed lightgray;
}

.container_spanCentrado {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.backButton {
  background-color: var(--primary);
  color: white !important;
  border-radius: 20px;
  height: 2.9rem;
  width: 2.9rem;
  box-shadow: 4px 3px 5px 0px rgba(0, 0, 0, 0.175);

  &:hover {
    background-color: #d3a233;
  }
}

.shadowHover{
  border-radius: 20px;
  transition: box-shadow 0.2s ease-in-out;
  &:hover {
    box-shadow: 4px 3px 5px 0px rgba(0, 0, 0, 0.175);
  }
}

.backgroundWhite{
  background-color: white;
}

.spanVertical {
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  transform: rotate(-90deg);
  position: absolute;
  color: #767676;
  font-size: 16px;
}

// HIGHTLIGHT
::selection {
  color: white;
  background: var(--primary);
}

::-moz-selection {
  color: white;
  background: var(--primary);
}
.tooltip-header {
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: 500;
  padding-bottom: 5px;
  border-bottom: 1px solid #c5c5c5;
}

body{overflow-x: hidden;}
.tooltip-body {
  width: 220px;
  display: flex;
  flex-direction: column;
}

.tooltip-row {
  width: 100%;
  display: inline-block;
}
.tooltip-row .series-name {
  font-weight: normal;
  opacity: 0.6;
  display: inline-block;
  line-height: 1.5;
  padding-right: 10px;
}

.tooltip-row .value-text {
  display: inline-block;
  line-height: 1.5;
  float: right;
}

.tooltip-header .value-text {
  display: inline-block;
  line-height: 1.5;
  float: right;
  font-size: 14px;
  padding: 1px 7px;
  border-radius: 15px;
  color: white;
  text-align: right;
}

.dx-texteditor.centerText {
  & input {
    text-align: center !important;
    padding-left: 0px !important;
    padding-right: 0px !important;
  }
}

.bg-secondary
{
  background-color: #9e9e9e;
}

// TEMPORAL HASTA NUEVO COMPONENTE RANGE SELECTOR
/* BOTONES DEL FILTRO DE TIEMPO */
#dxBox_filtroTiempo .dx-button {
  border: none;
  border-radius: 0px;
  background-color: transparent !important;
}

#dxBox_filtroTiempo .dx-button {
  border-right: 3px solid #d3d3d3;
}

#dxBox_filtroTiempo .dx-button:hover {
  background-color: transparent;
  border-right: 3px solid #808080;
}

.dxButton_activo {
  border-right: 3px solid #edb637 !important;
}

#dxBox_filtroTiempo .dx-button i {
  position: relative;
  top: calc(50% - 10px); /* 50% - 3/4 of icon height */
  right: 25%;
  font-size:21px;
  float:right;
}

#dxBox_filtroTiempo .dx-button:not(.dxButton_activo) i {
  color: #d5d5d5 !important;
}

#dxBox_filtroTiempo .dx-button:not(.dxButton_activo):hover i {
  color: #808080 !important;
}

.pointer {
  cursor: pointer;
}

.cursor-default {
  cursor: default;
}

.divider-top {
  border-top: 1px solid gainsboro;
}

.divider-right {
  border-right: 1px solid gainsboro;
}

.divider-left {
  border-left: 1px solid gainsboro;
}

.divider-bottom {
  border-bottom: 1px solid gainsboro;
}

.text-cut {
  text-overflow: ellipsis;
  white-space: nowrap !important;
  overflow: hidden;
}

.text-nocut {
  white-space: nowrap;
}

.media-body:before{
  z-index:-1;
  position: absolute;
  background-color: #FFFFFF;
  height:200%; //200% para que sobrepase al padre y se vea el fondo blanco tambin en los paddings.
  width:200%;
  margin-left: -20px;
  pointer-events: none;
  content: '';
}

@for $flex from 0 through 200 {
  .flex-#{$flex}px {
      flex: 0 0 $flex+px;
  }
}
.color-red{
