.DocumentosAdjuntosFiniquitos {
    .icon-badge-group .icon-badge-container {
        display: inline-block;
        margin-left:15px;
    }
    
    .icon-badge-group .icon-badge-container:first-child { 
      margin-left:0
    }
    
    .icon-badge-container {
        margin-top:7px;
        position:relative;
    }
    
    .icon-badge-icon {
        font-size: 30px;
        position: relative;
    }
    
    .icon-badge {
        background-color: var(--primary);
        font-size: 12px;
        color: white;
        font-weight: 700;
        text-align: center;
        width:20px;
        height:20px;
        border-radius: 35%;
        position: absolute; /* changed */
        top: -5px; /* changed */
        left: 15px; /* changed */

        display: flex;
        align-items: center;
        justify-content: center;
    }

    #DocumentosLista{
        .itemContainer > div {
            width: 96% !important;
        }
    }

    .documentos-invalid .tileContainer_archivos > div {
        color: #d9534f;
        
        &:not(.scroll_adjuntarArchivos)::after {
            pointer-events: none;
            font-weight: 700;
            margin-left: 10px;
            width: 18px;
            height: 18px;
            text-align: center;
            line-height: 18px;
            font-size: 13px;
            background-color: #d9534f;
            color: #fff;
            content: '!';
            border-radius: 50%;
        }
    }
}