.dx-progressbar-wrapper {
  position: relative;
}

.dx-progressbar-container {
  height: 1.5rem;
  background-color: var(--secondary);
  border: none;
  border-radius: 3px;
}

.dx-progressbar-range {
  background-color: var(--primary);
  border: none;
  border-radius: 3px;
}

.dx-progressbar-status {
  position: absolute;
  color: white;
  padding: 2px 10px;
  font-weight: 500;
  font-size: 0.8rem;
}