.card_multiselector {
  height: 100%;
  width: 300px;
  margin-right: 10px;
  padding: 3px;
  background-color: var(--white);
}

.chart_noData {
  background-color: rgba($color: #ffffff, $alpha: 1);
  opacity: 0;
  transform: translateY(100%);
  transition: opacity 0.5s ease, transform 0.5s ease;
  display: none; /* Inicialmente oculto */

  &.show {
    display: block;
    opacity: 1;
    transform: translateY(0);
  }
}
