.dx-data-row > .dx-Cell_Editable[role="gridcell"]
{
  outline: 2px solid lightgray;
  outline-offset: -1px;
}

.dx-data-row > .dx-Cell_Editable[role="gridcell"] .dx-texteditor-container {
  box-shadow: inset 0 0 0 0.75px lightgray;
}

.dx-data-row > .warningCell[role="gridcell"] > div {
  border-top: 1px solid #f88787 !important;
  border-left: 1px solid #f88787 !important;
  border-right: 1px solid #f88787 !important;
  border-bottom: 1px solid #f88787 !important;
}

.dx-datagrid-content .dx-datagrid-table .dx-row > td,
.dx-datagrid-content .dx-datagrid-table .dx-row > tr > td {
  vertical-align: middle !important;
}

.disabledRow {
  color: silver !important;
  & [role=gridcell] {
    color: silver !important;
  }
}

.disabledCell {
  color: silver !important;
}

.successRow {
  color: green !important;
  & [role=gridcell] {
    background-color: #e5fde5 !important;
  }
  & svg {
    fill: green !important;
  }

  &.dx-state-hover * {
    background-color: #d4e8d4 !important;
  }
}

.dangerRow {
  color: rgb(134, 132, 15) !important;
  & [role=gridcell] {
    background-color: #f3ecae  !important;
  }
  & svg {
    fill: rgb(134, 132, 15)!important;
  }

  &.dx-state-hover * {
    background-color: #f1edc7 !important;
  }
}

.warningRow {
  color: #a22a2a !important;
  & [role=gridcell] {
    background-color: #f4d7d7 !important;
  }
  &.dx-selection * {
    background-color: #f39f9f !important//var(--primary) !important;
  }
  &.dx-state-hover * {
    background-color: #f7b8b8 !important;
  }
  & svg {
    fill: white !important;
  }
}

.warningRow_onlyText{
  color: var(--danger) !important;
  td {
    color: var(--danger) !important;
  }
}

.nuevaRow{
  color: #1a9fff !important;
  & [role=gridcell] {
    background-color: #eafbff !important;
  }
  &.dx-selection * {
    background-color: var(--primary) !important;
  }
  & svg {
    fill: white !important;
  }
}

.customRow{
  color: #5e5e5e !important;
  & [role=gridcell] {
    background-color: #f9e6b9 !important;
  }
  &.dx-selection * {
    background-color: var(--primary) !important;
  }
  & svg {
    fill: white !important;
  }
}

.customRow.dx-state-hover{
  & [role=gridcell] {
    background-color: rgba(249,230,185,.5) !important;
  }
}

.no-border > .dx-datagrid {
  & .dx-datagrid-headers,
  & .dx-datagrid-rowsview {
    border: none;
  }

  & .dx-row > td,
  & .dx-row > tr > td {
    padding: 5px;
    border: none;
  }
}

//Placeholder de los editores del grid
.dx-datagrid .dx-row .dx-placeholder {
  width: 100%;
  text-align: center;
}

//Grid con columnas fijas
.dx-datagrid .dx-datagrid-table-fixed {
  .dx-row td {
    // background-color: white ;
  }
}

.headerRow-lg .dx-datagrid {
  .dx-header-row {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1rem;
    color: #817f80;
    height: 3.5rem;
  }
}

.dx-texteditor.dx-editor-outlined {
  background: #fff;
}
