#DashboardOperativo{
    @for $porc from 0 through 100 {
        .he-#{$porc} {
            height: percentage($porc /100);
        }
    }

    #dxScrollView_principal
    {
        .dx-scrollable-content:first-child
        {
            padding:0px;
        }

        .dx-scrollview-content:first-child{
            padding:0px;
        }
    }

    #Chart
    {
        height: calc(100% - 9px);
        width: 100%;
        svg {
            height: calc(100% + 10px);
        }
    }

    .he-card
    {
        height:320px;
    }

    @media (max-width: 1199px) {
        .col-xl-5ths {
            padding-bottom:10px;
        }
    }

    @media (min-width: 1200px) {
        .col-xl-5ths {
            flex:0 0 20%;
            max-width:20%;
        }
    }

    @media (min-width: 1200px) {
        .col-xl-40 {
            flex:0 0 40%;
            max-width:40%;
        }
    }

    @media (min-width: 1200px) {
        .col-xl-2 {
            flex:0 0 20%;
            max-width:20%;
        }
    }


    .CircularGauge 
    {
        width: 100%;
        height: 100%;
        svg
        {
            margin:auto;
        }
    }
   

    .dx-scrollview
    {
        .dx-scrollable-content:first-child{
            height:100%;
        }
        .dx-scrollview-content
        {
             height:100%;
            padding:0px;
            padding-top:5px;
            padding-bottom:5px;
        }

        .dx-scrollable-container
        {
         padding:5px;
         padding-top:0px;
         padding-bottom:0px;
        }
    }

        .font-horas {
            font-weight: 600;
            background-color: var(--light);
            border-radius: 10px;
            border-radius: 8px;
            margin: 0px 10px;
            padding: 4px 0px;
        }
}
