.dx-toolbar {
    &.dxToolbar_opciones {
      display: none;
      width: 0px !important;
  
      & .dx-toolbar-after {
        padding-left: 0px;
      }
  
      & .dxToolbar_opcion {
        height: 25px;
        width: 25px;
        background-color: #eaeaea;
        border-radius: 5px;
        -webkit-animation: roll-in-right 0.5s ease both;
        animation: roll-in-right 0.5s ease both;
  
        & svg {
          cursor: pointer;
          fill: rgb(120, 120, 120) !important;
        }
  
        &:hover {
          background-color: #edb637;
  
          & svg {
            fill: white !important;
          }

          & div {
            color: white !important;
          }
        }
      }
    }
  }

  .dx-item.dx-treeview-item .dxToolbar_opciones,
  .dx-item.dx-list-item .dxToolbar_opciones {
    display: none;
    width: 0px !important;
  }

  .dx-item.dx-treeview-item.dx-state-hover .dxToolbar_opciones,
  .dx-item.dx-list-item.dx-state-hover .dxToolbar_opciones,
  .dx-item.dx-list-item.dx-state-active .dxToolbar_opciones {
    display: block;
    width: 70px !important;
  }