.iconHeader_tarjeta_DashboardEjecutivo .dx-icon.dx-icon-export{
    font-size:x-large
}

 .MainTitle {
    color:#817f80;

    > .dx-multiview-wrapper{
        overflow:visible;
    }

     > div > div > div{
        overflow: visible;

        > .dx-multiview-item-content{
            position:relative;
            display:flex;
            flex-direction:column;
            flex: 1 1;
        }
     }
 }

.MainTitle:after{
    z-index: -1;
    position: absolute;
    background-color:#f5f5f5;
    top:0;
    left:0;
    height:100%;
    width: 100%;
    content: '';
}

.separadorHeader{
    display: flex;
    justify-content: center;
    align-items: center;

    div {
        border-left: 1px solid #ddd;
        width: 8px;
        height: 80%;
        margin: 0 25px;
    }
}