.iconCentered {
    position: relative;
    height: 100%;
    width: 100%;

    > i {
        position: absolute;
        transform: translate(-50%, -50%);
        font-size: 1.5rem;
        
    }
    
    :hover{
        cursor: pointer;
    }
}

.fillDropDownContent {
    height: 50rem;
    max-height: 100%;
    position: relative;

    > div {
        position: absolute;
        top: -10px;
        bottom: -10px;
        left: -10px;
        right: -10px;
    }
}

.dxDataGridRowHover .dx-data-row:not(.dx-selection):hover {
    background: white;
    cursor: pointer;
    filter: brightness(85%);
    -webkit-filter: brightness(85%);
}

.dateFilterPlus {
    .dx-texteditor-input {
        text-align: center;
        width: 100%;
        padding: 0 !important;
    }
}