// Sidebar Animation

.SidebarAnimation-appear {
  transform: translateX(-30px);
  opacity: 0;

  &.SidebarAnimation-appear-active {
    opacity: 1;
    transform: translateX(0);
    transition: all .4s linear;
  }
}

// Page Animation

.PageAnimation-appear {
  @extend .animated;
  @extend .fadeInUp;
}

// Header Animation

.HeaderAnimation-appear {
  transform: translateY(-30px);
  opacity: 0;

  &.HeaderAnimation-appear-active {
    opacity: 1;
    transform: translateY(0);
    transition: all .4s linear;
  }
}

// Main Animation

.MainAnimation-appear {
  transform: translateY(-30px);
  opacity: 0;

  &.MainAnimation-appear-active {
    opacity: 1;
    transform: translateY(0);
    transition: all .4s linear;
  }
}

//Impresin Animation

.ImpresionAnimation-appear {
  transform: translateX(-100vw);

  &.ImpresionAnimation-appear-active {
    transform: translateY(0);
    transition: all .4s linear;
  }
}

.ImpresionAnimation-enter {
  transform: translateX(-100vw);

  &.ImpresionAnimation-enter-active {
    transform: translateY(0);
    transition: all .4s linear;
  }
}


.ImpresionAnimation-leave {
  transform: translateX(0);

  &.ImpresionAnimation-leave-active {
    transform: translateY(-100vw);
    transition: all .4s linear;
  }
}


.CookieBannerAnimation-appear {
  transform: translateY(200px);

  &.CookieBannerAnimation-appear-active {
    transform: translateY(0);
    transition: all .5s cubic-bezier(.54,1.78,.13,.55)
  }
}

.CookieBannerAnimation-leave {
  transform: translateY(0);

  &.CookieBannerAnimation-leave-active {
    transform: translateY(200px);
    transition: all .5s cubic-bezier(.68,.29,.83,-0.58)
  }
}