@-webkit-keyframes roll-in-right {
    0% {
        -webkit-transform: translateX(80px) rotate(540deg);
        transform: translateX(80px) rotate(540deg);
        opacity: 0;
    }

    100% {
        -webkit-transform: translateX(0) rotate(0deg);
        transform: translateX(0) rotate(0deg);
        opacity: 1;
    }
}

@keyframes roll-in-right {
    0% {
        -webkit-transform: translateX(80px) rotate(540deg);
        transform: translateX(80px) rotate(540deg);
        opacity: 0;
    }

    100% {
        -webkit-transform: translateX(0) rotate(0deg);
        transform: translateX(0) rotate(0deg);
        opacity: 1;
    }
}

.border-alert {
    animation: border-alert-red 1s ease infinite;
  }
  
  @keyframes border-alert-red {
    50% {
    outline: 2px solid red
    }
  }

.border-alert-info {
    animation: border-alert-info 1s ease infinite;
}
@keyframes border-alert-info {
    75% {
        outline: 4px solid var(--info); //#3559ce
    }
}