#CardSelector {
    .groupTitle {
        // background-color: var(--primary);
        //color:white;
        background-color: rgb(245, 243, 243);
        //border-bottom: 3px solid var(--secondary);
    }

    .row-1 {
        height: 16.5rem;
        min-height: 150px;
    }

    .row-2 {
        height: 35rem;
        min-height: 150px;
    }

    .dx-scrollable-content,
    .dx-scrollview-content {
        padding: 7px;
    }

    .vMovil {
        .dx-scrollable-content,
        .dx-scrollview-content {
            padding: 1px;
        }
    }

    .card .dx-scrollable-content,
    .card .dx-scrollview-content {
        padding: 0px;
    }

    .border-width-2 {
        border-width: 2px !important;
    }

    .vMovil #dxScrollView {
        .dx-scrollable-content,
        .dx-scrollview-content {
            padding: 0;
        }
    }

    .CircularGauge_selector {
        height: 100%;
        & svg {
            height: 105%;
        }
    }
}
