// Progress Bar
.progress {

  position: relative;
  background-color: var(--secondary);
  padding: 1px;

  .progress-bar {
    &:last-child {
      @include border-right-radius($progress-border-radius);
    }

    & .progress-bar-info{
      position: absolute;
      top: 50%;
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
    }
  }

  &.progress-bar-sm {
    height: 0.5rem;
  }

  &.progress-bar-xs {
    height: 0.3rem;
  }

  &.progress-bar-lg {
    height: 24px;
  }

  &.progress-bar-xl {
    height: 34px;
  }

  &.progress-bar-rounded {
    @include border-radius(3px);
    & .progress-bar {
      @include border-radius(3px);
    }
  }
}

.progress-bar-animated-alt {
  &.progress-bar,
  .progress-bar {
    position: relative;

    &::after {
      content: "";
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $white;
      animation: progress-active 2s ease infinite;
    }
  }
}

@keyframes progress-active {
  0% {
    opacity: 0.4;
    width: 0;
  }
  100% {
    opacity: 0;
    width: 100%;
  }
}
