.DocumentosAdjuntos-container {
  .dx-scrollable-content {
    width: 100%;
  }

  .documento-container {
    height: 50px;
    position: relative;
    overflow: hidden;

    .documento-options-container {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;

      position: absolute;

      bottom: 0;
      right: -100px;

      width: fit-content;
      gap: 5px;

      transition: 0.2s ease;
    }

    &:hover {
      .documento-options-container {
        right: 0;
      }
    }
  }
}

.documento-icon-container {
  display: grid;
  align-content: center;
  justify-content: center;

  height: 100%;
  width: 100%;
}
