#InfInventarios{
    .dx-pivotgrid-horizontal-headers
    {
        background-color: rgba(221, 221, 221, 0.12);
        color:rgb(168, 168, 168);
    }

    #dxDataGrid_valoracionInventario{
        .dx-command-expand{
            padding:0;
        }

        .dx-select-checkbox{
            display: block;
            .dx-checkbox-icon{
                border-color:#ddd;
            }
        }
    }
    
    #dxDataGrid_finalTeorico_prendas_gridPrendas
    ,#dxDataGrid_finalTeorico_entidades_gridEntidades
    ,#dxDataGrid_analisisInventarios_entidades_gridPrendas
    {
        .dx-data-row.dx-state-hover > td {  
            cursor: pointer;  
        }  
    }

    #dxTabPanel_analisisEntreInventarios
    {
        .dx-tabpanel-container
        {
            padding-top:35px;
        }
    }
}
