#item_miCuenta 
{
    text-align: center;
    border:1px solid rgb(48,177,255);
    border-radius:3px;

    & span {
        color:#30b1ff;
        font-size:.8rem;
        font-weight: 500;
    }
}

#item_miCuenta:hover{
    background-color: rgb(48,177,255) !important;
    border-radius:3px;

    & span {
        color:white;
    };
}

