.dxc-arg-line,
.dxc-val-line {
  display: none;
}
.dxc-tooltip, .dx-popup-content {
  & .tooltip_header {
    display: flex;
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: 500;
    padding-bottom: 5px;
    border-bottom: 1px solid #c5c5c5;

    & .tooltip_title {
      flex: 1;
    }

    & .tooltip_value-text {
      padding: 1px 7px;
      border-radius: 15px;
      color: white;
      text-align: right;
    }
  }

  & .tooltip_body {
    width: 220px;

    & .tooltip_row {
      width: 100%;
      display: flex;

      & .tooltip_series-name {
        opacity: 0.6;
        flex: 1;
      }
    }
  }
}

.dxc-tooltip{
  z-index: 10;
}

.dx-popup-content{
  z-index: 1601;
}


.dxChart_responsive
    {
        height: calc(100% - 9px);
        width: 100%;
        svg {
            height: calc(100% + 10px);
        }
    }